.products {
	padding: 130px 0 55px 0;

	&__title {
		@include title;
	}

	&__categories {
		display: flex;
		row-gap: 45px;
		column-gap: 30px;
		justify-content: center;

		&-image {
			width: 255px;
			height: 190px;
			object-fit: cover;
		}
		&-title {
			font-size: 18px;
			margin-top: 10px;
		}
		&-info {
			font-size: 14px;
			color: gray;
		}

		@media (max-width: 992px) {
			gap: 20px;
		}

		@media (max-width: 768px) {
			flex-wrap: wrap;
		}
	}

	&__category {
		background-color: var(--light-color);
		color: var(--black-color);
		h1 {
			@include addInfo;
		}

		p {
			@include descript;
			color: var(--grey-color);
		}
	}

	&__category-2 {
		background-color: var(--white-color);
	}

	&__info {
		padding: 8px 0 16px 8px;
	}

	&__info-2 {
		display: flex;
		flex-direction: column;
		gap: 4px;
		h1 {
			@include titleUnder2;
		}
		p {
			color: var(--brown-color);
			@include descript2;
			text-transform: uppercase;
			font-weight: 400;
		}
	}

	.swiper-wrapper {
		overflow: hidden;
		width: 100%;
		height: auto;
		margin-bottom: 56px;
		justify-items: center;
	}

	.swiper-pagination {
		position: absolute;
		margin-bottom: 30px;
		bottom: 10%;
	}

	.swiper-slide {
		display: flex;
		align-items: center;
		justify-content: center;
		a {
			color: var(--black-color);
		}
	}

	.swiper-pagination {
		display: flex;
		justify-content: center;
		gap: 16px;
	}

	.swiper-pagination-bullet {
		width: 30px;
		height: 3px;
		background-color: var(--disable-color);
		// margin: 0 15px;
		border-radius: 0px;
	}

	.swiper-pagination-bullet-active {
		background-color: var(--brown-color);
		width: 30px;
		height: 3px;
		border-radius: 0px;
		// border-radius: 0px;
	}

	// products__sorts-item
	&__sort-list {
		position: relative;
		display: flex;
		justify-content: space-between;
		margin-bottom: 25px;
		margin-top: 56px;
		h1 {
			@include descript;
			font-weight: 400;
			text-transform: uppercase;
		}

		@media (max-width: 560px) {
			flex-direction: column;
			gap: 10px;
			justify-content: center;
			align-items: center;
		}
	}

	&__sort-button {
		position: relative;
		padding: 12px 94px 12px 68px;
		border: 1px solid var(--line-color);
		text-transform: uppercase;
		@include descript2;
		margin-bottom: 8px;
		display: flex;
		align-items: center;
		transition: transform 0.3s ease-in-out;
		&::before {
			position: absolute;
			content: "";
			left: 32px;
			top: 50%;
			transform: translateY(-50%);
			width: 20px;
			height: 20px;
			background-image: url("./../images/Products-page/menu-sort.svg");
			background-repeat: no-repeat;
			background-size: contain;
		}

		@media (max-width: 992px) {
			padding: 12px 74px 12px 58px;
		}
	}
	

	&-arrow {
		position: absolute;
		content: "";
		right: 65px;
		top: 50%;
		transform: translateY(-45%);
		width: 12px;
		height: 12px;
		background-image: url("./../images/Products-page/arrow-down.svg");
		background-repeat: no-repeat;
		background-size: contain;
		transform-origin: center;
		transition: transform 0.3s ease-in-out;
		&.active {
			transform: rotate(-180deg);
			transform: translateY(-70%) rotate(-180deg);
		}

		@media (max-width: 992px) {
			right: 35px;
		}
	}

	&__sort-menu {
		position: absolute;
		right: 0;
		padding: 16px 64px 16px 41px;
		background-color: var(--accent-gold-color);
		display: flex;
		flex-direction: column;
		gap: 8px;
		max-height: 0;
		overflow: hidden;
		opacity: 0;
		visibility: hidden;
		z-index: 99;
		transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out, max-height 0.4s ease-in-out, visibility 0.4s;

		li {
			@include descript;
			font-weight: 200;
			position: relative;
			cursor: pointer;
			&.active::before {
				content: "";
				position: absolute;
				left: -25px;
				top: 50%;
				transform: translateY(-50%);
				width: 13px;
				height: 13px;
				background-image: url("./../images/Products-page/done.svg");
				background-repeat: no-repeat;
				background-size: contain;
			}
			&:hover {
				font-weight: 300;
			}
			a {
				color: var(--black-color);
			}
		}

		&.active {
			max-height: 500px;
			opacity: 1;
			visibility: visible;
		}
	}
}
