.cabinet {
	padding: 130px 0 55px 0;

	&__info {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 36px;
		@media (max-width: 992px) {
			gap: 20px;
		}

		@media (max-width: 756px) {
			gap: 15px;
		}

		@media (max-width: 640px) {
			grid-template-columns: repeat(1, 1fr);
		}

		&-subscribe,
		&-order,
		&-about,
		&-exit {
			background-color: #ece9e3;
		}

		&-exit {
			padding: 32px 24px;
			@media (max-width: 756px) {
				padding: 25px 15px;
			}

			@media (max-width: 680px) {
			}
		}

		&-subscribe,
		&-order,
		&-about {
			padding: 32px 24px 56px 24px;
			@media (max-width: 756px) {
				padding: 25px 15px 40px 15px;
			}
		}

		&-title {
			@include addInfo;
			font-weight: 400;
			margin-bottom: 10px;
		}

		&-exit h1 {
			margin-bottom: 0;
		}

		&-exit {
			display: flex;
			align-items: center;
			justify-content: space-between;
			img {
				@media (max-width: 680px) {
					width: 25px;
					height: 25px;
				}
			}
		}
		&-wrap {
			display: flex;
			flex-direction: column;
			div,
			a {
				position: relative;
				display: flex;
				justify-content: space-between;
				align-items: center;
				border-bottom: 1px solid var(--line-color);
				cursor: pointer;
				&:hover {
					color: var(--brown-color); // This will change the text color of <p>

					p {
						color: var(--brown-color); // Ensure <p> color changes
					}

					svg path {
						fill: var(--brown-color);
						transform: translateX(5px);
						z-index: 99999;
						transition: all 0.3s ease-in-out;
					}
				}
				p {
					padding: 10px 16px;
					@include titleUnder;
					color: var(--black-color);
				}
				svg {
					transform: translateX(-10px);
					width: 22px;
					height: 22px;
					position: absolute;
					right: 10px;
					z-index: 99;
					height: 35%;
					width: 8%;
					fill: #151515;
					@media (max-width: 756px) {
						height: 30%;
					}
				}
			}
		}

		&-left,
		&-right {
			display: flex;
			flex-direction: column;
			gap: 36px;
			@media (max-width: 756px) {
				gap: 15px;
			}
		}
	}

	&-block {
	}
}

.account {
	&__label {
		color: var(--brown-color);
		@include descript;
		padding-left: 16px;
	}

	&__input-wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		border-bottom: 1px solid var(--line-color);
		// padding: 10px 16px;
		padding: 0 16px 5px 16px;
		@include titleUnder;
		margin-bottom: 16px;

		img {
			width: 18px;
			height: 18px;
			object-fit: contain;
			@media (max-width: 756px) {
				height: 15px;
				width: 15px;
			}

			// @media (max-width: 680px) {
			// 	width: 10px;
			// 	height: 10px;
			// }
		}
	}

	&__input[type="password"] {
		-webkit-text-security: disc;
		font-size: 30px;
		max-width: 150px;
		width: 100%;
	}

	&__input {
		font-weight: 300;
	}
}
