.calculator__circle-result,
.calculator__circle-result-2,
.calculator__circle {
	display: flex;
	align-items: center;
	justify-content: center;
	font-weight: 300;
	@include titleUnder;
	@media (max-width: 992px) {
		min-width: 45px;
		height: 45px;
	}
}

.private {
	margin-top: 56px;

	&__wrapper {
		display: flex;
		flex-direction: column;
		gap: 56px;
		@media (max-width: 840px) {
			flex-direction: column;
			gap: 30px;
		}
	}

	&__blocks {
		display: flex;
		gap: 230px;
		h1 {
			font-weight: 200;
			@include addInfo;
			margin-bottom: 8px;
		}

		@media (max-width: 840px) {
			flex-direction: column;
			gap: 30px;
		}
	}

	&__blocks-2 {
		display: flex;
		gap: 150px;

		h1 {
			font-weight: 200;
			@include addInfo;
			margin-bottom: 10px;
		}

		@media (max-width: 840px) {
			flex-direction: column;
			gap: 0px;
		}
	}

	&__top {
		height: 133px;
		max-width: 350px;
		width: 100%;
		// background-color: cadetblue;
		@media (max-width: 400px) {
			height: 10px;
		}
		&-decor {
			display: flex;
			align-items: center;
			justify-content: space-between;
			position: relative;

			.calculator__circle-result-2 {
				width: 53px;
				height: 53px;
				background-color: #e7e1dd;
				display: flex;
				align-items: center;
				justify-content: center;
				@include titleUnder;
				font-weight: 300;
				border-radius: 50%;
				@media (max-width: 992px) {
					width: 45px;
					height: 45px;
				}

				@media (max-width: 400px) {
					width: 35px;
					height: 40px;
				}
			}
		}

		@media (max-width: 768px) {
			height: 100px;
		}
	}

	&__descript {
		@include titleUnder3;
		font-weight: 300;
		@media (max-width: 600px) {
			max-width: 200px;
			width: 100%;
		}
	}
}

.private__unblock {
	margin-top: 90px;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-bottom: 112px;
	gap: 16px;
	@media (max-width: 768px) {
		margin-top: 20px;
		margin-bottom: 70px;
	}
	&-block {
		display: flex;
		align-items: center;
		gap: 4px;
		text-transform: uppercase;
		justify-content: space-between;
		padding: 16px;
		border: 1px solid var(--line-color);
		cursor: pointer;
		width: 100%;
		transition: all 0.3s ease-in;
		&:hover .private-show {
			opacity: 1;
			visibility: visible;
		}

		.private-show {
			opacity: 0;
			visibility: hidden;
			transition: all 0.2s ease-in;
			color: var(--brown-color);
		}
	}

	div {
		display: flex;
		align-items: center;
		gap: 4px;
		@include descript2;
		font-weight: 300;
	}
}

.private__services {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 36px;
	@media (max-width: 1120px) {
		gap: 20px;
	}

	@media (max-width: 1070px) {
		grid-template-columns: repeat(3, 1fr);
	}

	@media (max-width: 750px) {
		grid-template-columns: repeat(2, 1fr);
		gap: 15px;
	}

	@media (max-width: 480px) {
		grid-template-columns: repeat(1, 1fr);
	}

	&-service {
		padding: 24px 16px;
		background-color: var(--silver-color);
	}

	&-title {
		max-width: 250px;
		width: 100%;
		@include titleUnder2;

		font-weight: 400;
	}

	&-date {
		color: var(--brown-color);
		@include descript2;
		font-weight: 400;
		// margin-bottom: 0px;
	}

	&-list {
		display: flex;
		flex-direction: column;
		gap: 8px;
		margin-top: 24px;
		margin-bottom: 24px;
	}

	&-item {
		display: flex;
		// align-items: center;
		gap: 4px;
		@include descript;
		font-weight: 300;
		img {
			width: 20px;
			height: 20px;
		}
	}

	&-price {
		@include titleUnder;
		font-weight: 300;
	}

	&-btn {
		max-width: 160px;
		width: 100%;
		background-color: var(--accent-pink-color);
		color: var(--white-color);
		text-transform: uppercase;
		align-items: center;
		display: flex;
		justify-content: center;
		padding: 12px 0;
		margin-top: 32px;
		@include descript2;
	}
}

.private__services-service:first-child {
	h1 {
		margin-bottom: 45px;
	}

	.private__services-price {
		margin-top: 45px;
	}
}
