.authbox {
	position: relative;
	background-image: url("./../images/entry-register/bg.png");
	min-height: 100vh;
	width: 100%;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	padding: 20px;
	&__wrapper {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		max-width: 620px;
		height: 460px;
		width: 100%;
		background-color: var(--white-color);
		padding: 64px 36px;
	}
}

.login-form,
.register-form {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	opacity: 0;
	transform: translateY(-20px);
	padding: 54px 36px;
	pointer-events: none;
	transition: opacity 0.3s ease, transform 0.3s ease;
	@media (max-width: 576px) {
		padding: 70px 20px 20px 20px;
	}
	&::after {
		position: absolute;
		right: 36px;
		top: 36px;
		content: "";
		background-image: url("./../images/Favorites-page/close.svg");
		background-repeat: no-repeat;
		background-size: cover;
		width: 20px;
		height: 20px;
		cursor: pointer;

		@media (max-width: 576px) {
			width: 15px;
			height: 15px;
		}
	}
	&.active {
		opacity: 1;
		transform: translateY(0);
		pointer-events: all;
	}
	&__title {
		text-align: center;
		@include addInfo;
		font-weight: 400;
		margin-bottom: 24px;
		@media (max-width: 756px) {
			margin-bottom: 15px;
		}
	}

	&__register {
		display: flex;
		justify-content: center;
		color: var(--gold-color);
		@include descript;
		font-weight: 300;
		margin-bottom: 24px;
		@media (max-width: 576px) {
			margin-bottom: 15px;
		}
	}

	&__block {
		display: flex;
		flex-direction: column;
		gap: 24px;
		margin-bottom: 8px;
		input {
			padding: 10px 0 10px 16px;
			border: 1px solid #a8a29e;
			&::placeholder {
				color: #a8a29e;
				@include descript;
				font-weight: 300;
			}
		}

		@media (max-width: 576px) {
			margin-bottom: 15px;
		}
	}

	&__forgot {
		color: #a8a29e;
		@include descript;
		font-weight: 300;
		cursor: pointer;
		// margin-bottom: 24px;
	}

	&__btn {
		text-transform: uppercase;
		color: var(--white-color);
		display: flex;
		align-items: center;
		text-align: center;
		justify-content: center;
		margin-top: 24px;
		width: 100%;
		background-color: var(--accent-rose-color);
		padding: 16px;
		@include descript;
		font-weight: 600;
		@media (max-width: 576px) {
			margin-bottom: 15px;
		}
	}

	//
	#password,
	.login {
		width: 100%;
	}

	.check-wrapper {
		position: relative;
		width: 100%;

		&::before {
			position: absolute;
			right: 16px;
			top: 50%;
			transform: translateY(-50%);
			content: "";
			width: 18px;
			height: 18px;
			background-image: url("./../images/entry-register/good.svg");
			background-repeat: no-repeat;
			background-size: contain;
			opacity: 0;
			transition: opacity 0.3s ease;
		}

		&.error input {
			background-color: #ffe1e1;
		}

		&.success input {
			background-color: #e9f0fe;
		}

		.check-wrapper.success::before {
			opacity: 1;
		}
	}

	.password-wrapper {
		position: relative;
		width: 100%;

		&::before {
			position: absolute;
			right: 16px;
			top: 50%;
			transform: translateY(-50%);
			content: "";
			width: 18px;
			height: 18px;
			background-image: url("./../images/entry-register/close.svg");
			background-repeat: no-repeat;
			background-size: contain;
		}
	}

	.password-wrapper input.valid {
		border-color: #32ac37;
	}

	.password-wrapper input.invalid {
		border-color: #ffe1e1;
	}

	.password-wrapper input.default {
		border-color: #a8a29e;
	}
}
