.signUp {
	padding: 55px 0;
	background-color: var(--accent-gold-color);
	@media (max-width: 668px) {
		padding: 30px 0;
	}
	&-btn {
		background-color: var(--accent-pink-color);
		max-width: 255px;
		height: 55px;
		width: 100%;
	}
	&__container {
		width: 100%;
	}

	&__title {
		@include title;
		margin-bottom: 45px;
	}

	img {
		@media (max-width: 680px) {
			display: none;
		}
	}

	&__form {
		display: flex;
		justify-content: space-between;
		align-items: center;
		gap: 133px;
		@media (max-width: 992px) {
			gap: 50px;
		}
	}

	.form-container {
		max-width: 600px;
		width: 100%;
		padding: 20px;
		border: 1px solid #eaeaea;
		box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
	}

	form {
		display: flex;
		flex-direction: column;
		gap: 10px;
	}

	label {
		margin-bottom: 15px;
	}

	input[type="text"],
	input[type="email"],
	input[type="tel"] {
		max-width: 643px;
		width: 100%;
		border-bottom: 1px solid #dcdcdc;
		border-radius: 4px;
		font-size: 16px;
		padding: 10px;
	}

	.checkbox-group {
		margin-bottom: 20px;
	}

	.checkbox-group label {
		font-size: 12px;
		display: flex;
		align-items: center;
		gap: 10px;
		margin-bottom: 10px;
		color: var(--black-color);
		@media (max-width: 668px) {
			font-size: 10px;
		}
	}

	input::placeholder {
		color: var(--dark-grey-color);
		font-size: 16px;
		@media (max-width: 668px) {
			font-size: 13px;
		}
	}

	input[type="checkbox"] {
		-webkit-appearance: none;
		-moz-appearance: none;
		appearance: none;
		min-width: 20px;
		height: 20px;
		background-color: transparent;
		background-color: var(--accent-gold-color);
		accent-color: var(--dark-grey-color);
		border: 2px solid var(--dark-grey-color);
		display: inline-block;
		position: relative;
		cursor: pointer;
	}

	input[type="checkbox"]:checked {
		background-color: transparent;
		border-color: gray;
	}

	input[type="checkbox"]:checked::after {
		content: "";
		position: absolute;
		top: 2px;
		left: 5px;
		width: 5px;
		height: 10px;
		border: solid gray;
		border-width: 0 2px 2px 0;
		transform: rotate(45deg);
	}

	input[type="checkbox"]:disabled {
		border-color: lightgray;
		cursor: not-allowed;
	}

	input[type="checkbox"]:disabled:checked::after {
		border-color: lightgray;
	}
	button {
		display: flex;
		color: var(--white-color);
		border: none;
		cursor: pointer;
		text-align: center;
		align-items: center;
		justify-content: center;
		text-transform: uppercase;
		@include btn;
	}
}
