.club {
	height: 800px;
	background-color: var(--white-color);
	padding-top: 75px;
	color: var(--black-color);
	display: flex;
	align-items: center;
	width: 100%;
	@media (max-width: 668px) {
		height: 600px;
		min-height: 100%;
	}

	&__wrapper {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		height: 100%;
		position: relative;
		&::before {
			position: absolute;
			content: "";
			right: 0;
			top: 0;
			width: 50%;
			height: 100%;
			background-color: rgba(104, 100, 108, 0.497);
			background-image: url("./../images/Club-page/bg.png");
			background-position: center;
			background-repeat: no-repeat;
			background-size: cover;
			@media (max-width: 992px) {
				background-position: left;
			}

			@media (max-width: 786px) {
				position: relative;
				background-position: center;
			}

			@media (max-width: 480px) {
				display: none;
			}
		}
	}

	&__container {
		width: 100%;
	}

	&__blocks {
		display: flex;
		align-items: center;
		justify-content: space-between;
		height: 100%;
		width: 100%;
	}

	&__list {
		margin-top: -20px;
		margin-bottom: 95px;
	}
}

.membership {
	padding: 55px 0;
	background-color: var(--accent-gold-color);
	@media (max-width: 668px) {
		padding: 30px 0;
	}
	&__title {
		@include title;
		text-align: center;
		margin-bottom: 50px;
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		align-items: center;
		height: 430px;
		@media (max-width: 700px) {
			grid-template-columns: repeat(2, 1fr);
			gap: 20px;
			height: 100%;
		}
	}

	&__item {
		position: relative;
		border-right: 1px solid #ccc;
		text-align: center;
		cursor: pointer;
		overflow: hidden;
		max-width: 280px;
		width: 100%;
		height: 430px;
		display: flex;
		&:hover .membership__image {
			opacity: 1;
			transform: translateY(0);
			height: 158px;
			transition: all 0.3s ease-in-out;
		}

		&:first-child {
			border-left: 1px solid #ccc;
		}

		@media (max-width: 700px) {
			height: 300px;
		}
	}

	&__content {
		max-width: 160px;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		margin: 0 auto;
		text-transform: uppercase;
		color: var(--grey-color);
		@include descript;
		transition: transform 0.3s ease-in-out;
		gap: 30px;
		&:hover {
			color: var(--black-color);
		}
	}

	&__image {
		position: relative;
		background-size: cover;
		background-position: center;
		height: 0px;
		max-width: 220px;
		width: 100%;
		opacity: 0;
		transition: opacity 0.4s ease, transform 0.4s ease;
		background-position: center;
		background-repeat: no-repeat;
		background-size: contain;
		transform: translateY(100px);

		&--1 {
			background-image: url("./../images/Club-page/section-2.png");
		}

		&--2 {
			background-image: url("./../images/Club-page/section-2.png");
		}

		&--3 {
			background-image: url("./../images/Club-page/section-2.png");
		}

		&--4 {
			background-image: url("./../images/Club-page/section-2.png");
		}
	}
}

.feature {
	padding: 55px 0;
	background-color: var(--white-color);
	@media (max-width: 668px) {
		padding: 30px 0;
	}
	&__blocks {
		display: flex;
		flex-direction: column;
		gap: 50px;
	}
	&__container {
		width: 100%;
	}

	&__title {
		@include title;
		@media (max-width: 480px) {
			margin-bottom: -30px;
		}
	}

	&__list {
		display: flex;
		flex-direction: column;
		gap: 20px;
	}

	&__wrap {
		position: relative;
		img {
			max-width: 240px;
			width: 100%;
		}
	}

	&__item {
		position: relative;
		display: flex;
		align-items: center;
	}

	&__content {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
		max-width: 545px;
		width: 100%;
		text-transform: uppercase;
		border-bottom: 1px solid #333;
		padding: 10px 10px 10px 20px;
		cursor: pointer;

		@media (max-width: 1200px) {
			max-width: 450px;
		}
		@media (max-width: 860px) {
			max-width: 300px;
		}
		@media (max-width: 500px) {
			max-width: 250px;
		}
		@media (max-width: 450px) {
			max-width: 200px;
			padding: 10px 10px 10px 0px;
		}
		@media (max-width: 390px) {
			max-width: 170px;
		}

		span {
			@include titleUnder;
			width: 100%;

			@media (max-width: 500px) {
				font-size: 14px;
			}

			@media (max-width: 450px) {
				font-size: 12px;
			}

			@media (max-width: 450px) {
				font-size: 10px;
			}
		}

		svg {
			position: absolute;
			right: 10px;
			overflow: visible;
			will-change: transform;
			@media (max-width: 680px) {
				width: 25px;
			}
			@media (max-width: 450px) {
				width: 20px;
			}
			path {
				transition: transform 0.3s ease-in-out, fill 0.3s ease-in-out;
			}
		}

		&:hover {
			color: var(--brown-color);

			svg path {
				fill: var(--brown-color);
				transform: translateX(5px);
			}
		}

		&:hover + .feature__details {
			opacity: 1;
			transform: translateY(0);
			visibility: visible;
			transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
		}
	}

	&__img {
		position: absolute;
		right: 0;
		bottom: 0;
		@media (max-width: 1200px) {
			max-width: 200px;
			width: 100%;
			height: 210px;
		}
		@media (max-width: 700px) {
			display: none;
		}
	}

	&__details {
		position: absolute;
		right: 25%;
		top: 0;
		max-width: 150px;
		width: 100%;
		opacity: 0;
		visibility: hidden;
		background-color: var(--white-color);
		color: var(--black-color);
		border-radius: 4px;
		transform: translateY(10px);
		transition: all 0.3s ease-in-out;
		@include descript;
		@media (max-width: 860px) {
			right: 30%;
		}
		@media (max-width: 700px) {
			right: 0%;
		}

		@media (max-width: 400px) {
			max-width: 100px;
		}
	}
}

.advantage {
	padding: 55px 0;
	@media (max-width: 668px) {
		padding: 30px 0;
	}

	&__container {
		width: 100%;
	}

	&__title {
		@include title;
		max-width: 550px;
		width: 100%;
		margin: 0 auto;
		text-align: center;
		margin-bottom: 50px;
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: 40px;
		text-align: center;
		max-width: 100%;
		height: 100%;
		@media (max-width: 992px) {
			gap: 20px;
		}

		@media (max-width: 992px) {
			grid-template-columns: repeat(2, 1fr);
		}

		@media (max-width: 576px) {
			display: flex;
			flex-wrap: wrap;
			margin: 0 auto;
			justify-content: center;
			align-items: center;
		}

		img {
			max-width: 140px;
			width: 100%;
			max-height: 140px;
			transition: transform 0.5s ease;

			@media (max-width: 576px) {
				max-height: 120px;
			}
		}

		h1 {
			margin-top: 8px;
			margin-bottom: 10px;
			@include titleUnder;
		}

		p {
			max-width: 230px;
			width: 100%;
			margin: 0 auto;
			@include descript;
		}
	}

	&__item {
		cursor: pointer;
		background-color: var(--accent-gold-color);
		max-width: 350px;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		transition: all 0.3s ease-in-out;
		&:hover {
			background-color: #fffbeb;
		}
		@media (max-width: 992px) {
			height: 300px;
		}
		@media (max-width: 576px) {
			height: 200px;
		}

		&.advantage__content {
			text-align: center;

			&.advantage__content-wrap {
				overflow: hidden;
				max-height: 0;
				opacity: 0;
				transition: max-height 0.5s ease, opacity 0.8s ease-in-out;
			}
		}

		&:hover .advantage__content .advantage__content-wrap {
			max-height: 500px;
			opacity: 1;
		}

		&:hover h1 {
			transition: all 0.2s ease-in-out;
			color: var(--gold-color);
		}

		&:hover img {
			transform: translateY(-10px);
		}

		&:hover + .feature__details {
			opacity: 1;
			transform: translateX(0);
			visibility: visible;
			transition: all 0.3s ease-in-out;
		}
	}

	&__item-bg {
		position: relative;

		&::before {
			position: absolute;
			content: "";
			left: 0;
			background-image: url("../images/Club-page/06.svg");
			background-position: center;
			background-size: cover;
			width: 100%;
			height: 100%;
		}
	}

	&__item:not(last-child) {
		cursor: pointer;
		background-color: var(--accent-gold-color);
		max-width: 350px;
		width: 100%;
		height: 370px;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		overflow: hidden;
		@media (max-width: 992px) {
			height: 300px; // нова висота
		}

		& .advantage__content {
			text-align: center;
			width: 100%;

			& .advantage__content-wrap {
				overflow: hidden;
				max-height: 0;
				opacity: 0;
				transition: max-height 0.5s ease, opacity 0.5s ease-in-out;
			}
		}

		&:hover .advantage__content .advantage__content-wrap {
			max-height: 500px;
			opacity: 1;
		}

		&:hover h1 {
			transition: all 0.2s ease-in-out;
			color: var(--gold-color);
		}

		&:hover img {
			transform: translateY(-10px);
		}
	}
}

