.pricing {
	padding: 55px 0;
	text-align: center;
	@media (max-width: 668px) {
		padding: 20px 0;
	}

	&__title {
		margin-bottom: 60px;
		color: #333;
		@include title;
	}

	&__table {
		width: 100%;
		margin: 0 auto;
		border-spacing: 0;
		tr:not(:last-child):nth-child(odd) {
			background-color: #f2f2f2;
		}
	}

	&__table th:not(:first-child) {
		position: relative;
		&::after {
			position: absolute;
			left: 10px;
			top: -10px;
			content: "Тариф";
			color: var(--brown-color);
			@include titleUnder2;
			@media (max-width: 680px) {
				font-size: 12px;
				top: -5px;
			}

			@media (max-width: 400px) {
				font-size: 10px;
				top: -10px;
			}
		}
	}

	&__table td img {
		width: 30px;
		height: 30px;
		@media (max-width: 992px) {
			width: 25px;
			height: 25px;
		}

		@media (max-width: 680px) {
			width: 20px;
			height: 20px;
		}

		@media (max-width: 440px) {
			width: 18px;
			height: 18px;
		}
	}

	&__table th,
	&__table td {
		justify-content: end;
		text-align: left;
		padding: 15px;
		p {
			max-width: 240px;
			width: 100%;
		}
		@media (max-width: 756px) {
			padding: 10px;
		}
		@media (max-width: 575px) {
			p,
			span {
				line-height: 15px;
			}
		}
		@media (max-width: 440px) {
			padding: 5px;
			p {
				line-height: 12px;
				font-size: 12px;
				// margin-bottom: 1px;
			}
		}
	}

	.last {
		margin-bottom: 100px;
	}

	td:nth-child(1) {
		text-align: left;
		@include descript;
	}
	tbody tr td:first-child {
		padding-right: 100px;
		@media (max-width: 1200px) {
			padding-right: 30px;
		}
		@media (max-width: 620px) {
			padding-right: 20px;
		}

		@media (max-width: 440px) {
			padding-right: 5px;
		}
	}

	tbody tr td:not(:first-child),
	:not(:last-child) {
		padding-right: -20px;
		@media (max-width: 615px) {
			padding-right: 0px;
		}
	}

	&__table th {
		color: #555;
		@include addInfo;
		font-weight: 400;
	}

	&__table td:nth-child(1) {
		text-align: left;
	}

	&__table td:nth-child(2),
	td:nth-child(3),
	td:nth-child(4) {
		text-align: left;
	}

	&__cost td:not(:first-child) {
		@include titleUnder;
		font-weight: 400;
		@media (max-width: 756px) {
			font-size: 15px;
		}
		@media (max-width: 580px) {
			font-size: 12px;
			span {
				line-height: 70%;
			}
		}
		@media (max-width: 480px) {
			font-size: 10px;
			span {
				line-height: 70%;
			}
		}
	}

	.btn {
		display: inline-block;
		padding: 12px 5px;
		max-width: 158px;
		width: 100%;
		@include descript2;
		color: #fff;
		border: none;
		cursor: pointer;
		text-transform: uppercase;
		background-color: var(--accent-pink-color);
		align-items: center;
		justify-content: center;
		text-align: center;
		@media (max-width: 992px) {
			max-width: 108px;
		}
		@media (max-width: 756px) {
			margin-top: -10px;
		}
		@media (max-width: 680px) {
			max-width: 100px;
		}

		@media (max-width: 580px) {
			margin-top: -15px;
			padding: 8px 5px;
		}

		@media (max-width: 400px) {
			padding: 5px;
			font-size: 10px;
		}

		@media (max-width: 370px) {
			padding: 5px;
			font-size: 8px;
		}
	}
}

.response2 {
	padding: 55px 0;
	background-color: var(--silver-color);

	@media (max-width: 668px) {
		padding: 30px 0;
	}

	&__container {
		img {
			width: 40px;
			height: 40px;
			border-radius: 50%;
			border: none;
			object-fit: contain;
		}
	}

	&__title {
		@include title;
		text-align: center;
		margin-bottom: 20px;
	}

	&__descript {
		@include titleUnder2;
		max-width: 740px;
		width: 100%;
		text-align: center;
		margin: 0 auto;
		margin-bottom: 56px;
	}

	&__blocks {
		.swiper-slide {
			transition: opacity 0.3s ease, transform 0.3s ease;
		}
	}

	&__block {
		display: flex;
		flex-direction: column;
		text-align: center;
		max-width: 550px;
		margin: 0 auto;
		gap: 10px;
		overflow-x: hidden;
		// cursor: pointer;
		margin-bottom: 50px;
		user-select: none;
		z-index: 99;
		padding-top: 20px;
		transition: opacity 0.3s, transform 0.3s;
		opacity: 0.5; /* Неактивний слайд */
		@media (max-width: 1500px) {
			width: 450px;
		}

		@media (max-width: 800px) {
			width: 350px;
		}

		@media (max-width: 400px) {
			width: 320px;
		}
