	.swiper-pagination {
		display: flex;
		justify-content: center;
		gap: 10px;
		margin-top: 50px;
		position: absolute;
		margin-bottom: 30px;
		bottom: 10%;
		user-select: none;
	}

	.swiper-pagination-bullet {
		width: 32px;
		height: 3px;
		background-color: #aaa;
		border-radius: 0%;
		opacity: 1;
		cursor: pointer;
		user-select: none;

		&-active {
			background-color: var(--brown-color);
		}
	}

	.swiper-btns-2 {
		position: absolute;
		bottom: -5%;
		left: 50%;
		transform: translate(-50%, -50%);
		display: flex;
		justify-content: space-around;
		gap: 800px;
		z-index: 999;
		@media (max-width: 1500px) {
			gap: 650px;
		}

		@media (max-width: 1200px) {
			gap: 500px;
		}

		@media (max-width: 950px) {
