			@media (max-width: 576px) {
				width: 25px;
				height: 25px;
				margin: 0 30px;
			}
		}
	}

	.swiper-slide {
		z-index: 1; // виправлено
		opacity: 0.5;
	}

	.swiper-slide-active {
		opacity: 1;
		z-index: 10; // активний слайд має бути зверху
	}
