		.basket__content-table {
			width: 100%;
			border-collapse: collapse;

			thead {
				display: grid;
				grid-template-columns: 2fr 1fr 1fr 1fr 1fr;
				border-bottom: 1px solid var(--line-color);
				@media (max-width: 576px) {
					grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
				}
				th {
					@include titleUnder;
					color: var(--black-color);
					font-weight: 300;
				}

				tr {
					display: contents;
				}

				.basket__header {
					padding: 10px;
					text-align: left;
					padding-bottom: 30px;
					@media (max-width: 576px) {
						padding-bottom: 15px;
						font-size: 16px;
					}
					@media (max-width: 576px) {
						font-size: 14px;
					}
				}
			}

			tbody {
				display: grid;
				grid-template-columns: 2fr 1fr 1fr 1fr 1fr;
				align-items: center;
				border-bottom: 1px solid var(--line-color);

				@media (max-width: 576px) {
					grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
				}
				.basket__content-item {
					display: contents;

					.basket__content-product {
						padding: 15px 0px;
						display: flex;
						gap: 10px;
						align-items: center;
						@media (max-width: 576px) {
							padding: 5px 0px;
							gap: 5px;
						}

						.basket__img {
							width: 60px;
							height: 135px;
							flex-shrink: 0;
							overflow: hidden;
							border-radius: 5px;
							@media (max-width: 576px) {
								height: 100px;
							}
							img {
								width: 100%;
								height: 100%;
								object-fit: cover;
							}
						}

						.basket__product-info {
							text-align: center;
							.basket__product-name {
								@include descript;
								font-weight: 500;
							}

							.basket__product-brand {
								color: var(--grey-color);
								@include descript;
								font-weight: 200;
							}
						}
					}

					.basket__content-price,
					.basket__content-subtotal {
						@include titleUnder3;
						font-weight: 400;
						color: #32ac37;
					}

					.basket__content-delete {
						@include titleUnder;
						@media (max-width: 576px) {
							font-size: 13px;
						}
					}

					.basket__content-price,
					.basket__content-quantity,
					.basket__content-subtotal,
					.basket__content-delete {
						padding: 10px;
						text-align: left;
					}

					.basket__content-quantity {
						max-width: 100px;
						width: 100%;
						// padding: 5px;
						@include titleUnder2;
						background-color: var(--silver-color);
						@media (max-width: 576px) {
							font-size: 14px;
							max-width: 60px;
						}
					}

					.basket__content-quantity {
						input {
							width: 50px;
							text-align: center;
						}
					}

					.basket__content-delete {
						a {
							text-decoration: underline;
							color: var(--grey-color);
						}
					}
				}
			}

			@media (max-width: 576px) {
				.basket__content-product {
					display: flex;
					flex-direction: column;
					text-align: left;
					align-items: flex-start;
					width: 50%;
					padding-left: 0;
				}
			}
		}

		//

		&-summary {
			display: flex;
			flex-direction: column;
			h1 {
				@include addInfo;
				margin-bottom: 40px;
			}
		}

		&-line {
			max-width: 930px;
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding-bottom: 15px;
			border-bottom: 1px solid var(--line-color);
			margin-bottom: 16px;
			p {
				@include titleUnder;
			}
		}

		&-total {
			@include titleUnder2;
			&.active {
				color: #32ac37;
			}
		}

		&-checkout {
			display: inline-block;
			text-align: center;
			text-transform: uppercase;
			margin-top: 24px;
			color: var(--white-color);
			background-color: var(--accent-pink-color);
			max-width: 255px;
			width: 100%;
			padding: 16px;
			transition: all 0.2s ease-in-out;
			&:hover {
				background-color: var(--accent-rose-color);
			}
		}
	}
}

.basket__content-wrap {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-top: 40px;
	gap: 30px;
	margin-bottom: 48px;
	@media (max-width: 576px) {
		flex-direction: column;
		margin-bottom: 20px;
		gap: 10px;
		align-items: start;
	}
	.reset-btn {
		display: inline-block;
		max-width: 200px;
		width: 100%;
		padding: 12px;
		text-transform: uppercase;
		background-color: var(--light-color);
		color: var(--brown-color);
		@include descript2;
		text-align: center;
		transition: all 0.2s ease-in-out;

		@media (max-width: 576px) {
			max-width: 150px;
		}
		&:hover {
			background-color: var(--accent-pink-color);
			color: var(--white-color);
		}
	}
}

.coupon {
	display: flex;
	gap: 16px;
	align-items: center;
	input {
		border: 1px solid var(--line-color);
		padding: 12px 16px;
		max-width: 180px;
		width: 100%;
		text-transform: uppercase;
		&::placeholder {
			color: var(--grey-color);
			@include descript2;
		}
		@media (max-width: 576px) {
			padding: 10px 16px;
		}
	}

	&__button {
		display: inline-block;
		color: var(--black-color);
		align-items: center;
		text-align: center;
		max-width: 170px;
		width: 100%;
		text-transform: uppercase;
		@include descript2;
		background-color: var(--light-color);
		padding: 15px 16px;
		transition: all 0.2s ease-in-out;
		&:hover {
			background-color: var(--accent-pink-color);
		}

		@media (max-width: 576px) {
			max-width: 150px;
		}
	}
}
