.main {
	min-height: 100vh;
	background-color: var(--white-color);
	padding-top: 75px;
	color: var(--black-color);
	display: flex;
	align-items: center;
	width: 100%;
	@media (max-width: 668px) {
		min-height: 100%;
		padding: 110px 0 40px 0;
	}

	&::after {
		position: absolute;
		content: "";
		right: 0;
		top: 0;
		background-color: var(--grey-light-color);
		height: 75%;
		max-width: 605px;
		width: 100%;
		z-index: 0;
		@media (max-width: 1500px) {
			height: 75%;
			width: 35%;
			padding: 0;
		}
		@media (max-width: 668px) {
			height: 50%;
			width: 45%;
			padding: 0;
		}

		@media (max-width: 480px) {
			height: 35%;
			width: 60%;
			padding: 0;
		}
	}

	&__container {
		width: 100%;
	}

	&__blocks {
		display: flex;
		align-items: center;
		justify-content: space-between;
		height: 100%;
		width: 100%;
		gap: 80px;
		@media (max-width: 992px) {
			gap: 30px;
		}

		@media (max-width: 668px) {
			flex-direction: column-reverse;
			text-align: center;
			z-index: 99;
			padding: 45px 0;
		}

		@media (max-width: 480px) {
			padding: 0;
		}
	}

	p {
		text-transform: uppercase;
		@include descript;
	}

	&__title {
		font-size: 70px;
		font-weight: 200;
		span {
			color: var(--brown-color);
			font-weight: 300;
		}

		@media (max-width: 992px) {
			font-size: 60px;
		}
	}

	&__descript {
		max-width: 545px;
		width: 100%;
		font-size: 23px;
		margin-bottom: 56px;
		font-weight: 400;
		@media (max-width: 992px) {
			font-size: 18px;
		}

		@media (max-width: 480px) {
			font-size: 10px;
			line-height: 9%;
			margin-bottom: 30px;
		}
	}

	&-btn {
		@include btn;
		background-color: transparent;
		border: none;
		border: 1px solid var(--grey-color);
		text-transform: uppercase;
		font-weight: 400;
		display: inline-flex;
		color: var(--black-color);
		// display: flex;
		align-items: center;
		text-align: center;
		justify-content: center;
		// margin: 0 auto;
	}

	&__img {
		z-index: 9;

		@media (max-width: 1550px) {
			max-width: 450px;
			width: 100%;
			height: 455px;
		}

		@media (max-width: 992px) {
			max-width: 390px;
			width: 100%;
			height: 350px;
		}

		@media (max-width: 668px) {
			max-width: 300px;
			width: 100%;
			height: 300px;
			margin-bottom: 20px;
		}

		@media (max-width: 480px) {
			width: 205px;
			height: 200px;
			margin-bottom: 20px;
		}
	}
}

/* Наша мисия */

.mission {
	background-color: var(--silver-color);
	padding: 56px 0;
	@media (max-width: 668px) {
		padding: 30px 0;
	}
	&__blocks {
		display: flex;
		align-items: center;
		justify-content: center;
		gap: 133px;
		@media (max-width: 992px) {
			gap: 50px;
		}
		@media (max-width: 668px) {
			flex-direction: column;
			justify-content: center;
			text-align: center;
			margin: 0 auto;
			gap: 15px;
		}
	}

	&__content {
		max-width: 546px;
		width: 100%;
		@media (max-width: 480px) {
			margin-top: 50px;
		}
	}

	&__title {
		position: relative;
		margin-bottom: 20px;
		padding-bottom: 20px;
		@include title;
		@media (max-width: 480px) {
			margin-bottom: 0;
		}
		@include decor;
	}

	&__descript {
		margin-bottom: 15px;
		@include descript;

		@media (max-width: 480px) {
		}
	}

	&__list li {
		position: relative;
		padding-left: 20px;
		line-height: 140%;
		@include descript;
		@media (max-width: 668px) {
			text-align: left;
		}

		&::before {
			content: "";
			position: absolute;
			left: 10px;
			top: 0;
			width: 2px;
			height: 2px;
			background-color: var(--grey-color);
			border-radius: 50%;
			margin-top: 0.5em;
		}
	}

	&__img {
		max-width: 255px;
		width: 100%;
		height: 370px;
		@media (max-width: 668px) {
			height: 280px;
			img {
				width: 200px;
				max-height: 280px;
				object-fit: cover;
			}
		}

		@media (max-width: 480px) {
			height: 170px;
			img {
				width: 185px;
				max-height: 215px;
				object-fit: cover;
			}
		}
	}
}

