/* Загальний стиль для акардеону */
.accordion__blocks {
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: 20px;
	@media (max-width: 768px) {
		flex-direction: column;
		gap: 50px;
		margin-top: 50px;
	}

	@media (max-width: 480px) {
		img {
			width: 200px;
			height: 190px;
		}
	}
}
.accordion-container {
	width: 100%;
	max-width: 550px;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	gap: 15px;
	@media (max-width: 992px) {
		max-width: 450px;
	}

	@media (max-width: 880px) {
		max-width: 100%;
	}
}

.accordion {
	// overflow: hidden;

	&__intro {
		position: relative;
		display: flex;
		align-items: center;
		padding-bottom: 10px;
		font-size: 24px;
		cursor: pointer;
		text-transform: uppercase;
		transition: all 0.3s ease-in-out;
		@media (max-width: 880px) {
			font-size: 18px;
		}

		@media (max-width: 880px) {
			font-size: 15px;
		}

		i {
			position: absolute;
			right: 0;
			transition: transform 0.3s ease-in-out;
			font-size: 40px;
			&.active {
				transform: rotate(-45deg);
			}

			@media (max-width: 480px) {
				font-size: 30px;
			}
		}
		&.active {
			color: var(--brown-color);
		}
	}

	&__content {
		max-height: 0;
		overflow: hidden;
		will-change: max-height;
		transition: all 0.25s ease-out;
		opacity: 0;
		text-align: left;

		@media (max-width: 480px) {
			text-align: left;
		}
	}

	h4 {
		font-size: 26px;
		font-weight: 500;
		@media (max-width: 880px) {
			font-size: 20px;
		}
	}

	p {
		font-size: 15px;
		margin-bottom: 10px;
		@include descript;
		text-align: left;

		@media (max-width: 480px) {
			text-align: left;
		}
	}

	&__active {
		.accordion__content {
			opacity: 1;
		}
	}
}

.line {
	border-bottom: 1px solid;
	border-image: linear-gradient(to right, #717171 0%, #d1b496 100%);
	border-image-slice: 1;
}

