.author {
	padding: 130px 0 55px 0;
	@media (max-width: 668px) {
		padding: 100px 0;
	}
	&__conteiner {
		width: 100%;
	}

	&__blocks {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 50px;
		@media (max-width: 768px) {
			gap: 40px;
		}
		@media (max-width: 662px) {
			flex-direction: column;
			img {
				max-width: 250px;
				width: 100%;
				height: 100%;
			}
		}

		@media (max-width: 480px) {
			flex-direction: column;
			img {
				max-width: 200px;
				width: 100%;
				height: 100%;
			}
		}
	}

	&__contents {
		max-width: 640px;
		width: 100%;
		@media (max-width: 1200px) {
			max-width: 600px;
		}
		@media (max-width: 768px) {
			max-width: 400px;
		}
		@media (max-width: 662px) {
			max-width: 100%;
		}

		a {
			text-decoration: underline;
		}
	}

	&__about {
		position: relative;
		font-size: 18px;
		color: var(--grey-color);
		padding-bottom: 10px;
		// max-width: 370px;
		// width: 100%;

		&::before {
			position: absolute;
			bottom: 0px;
			left: 0;
			width: 80%;
			content: "";
			border-bottom: 3px solid;
			border-image: linear-gradient(to right, #717171 0%, #d1b496 50%, #ffffff 100%);
			border-image-slice: 1;
		}
	}

	&__descript {
		margin-top: 25px;
		margin-bottom: 20px;
		display: flex;
		flex-direction: column;
		gap: 10px;
		@include descript;
	}

	&__title {
		@include title;
	}

	&-btn {
		font-size: 16px;
		color: var(--brown-color);
	}
}

/* education section*/

.education {
	position: relative;
	padding: 55px 0;
	z-index: 9;
	@media (max-width: 668px) {
		padding: 20px 0;
	}

	&::after {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 0;
		width: 100%;
		height: 70%;
		background-color: var(--silver-color);
		content: "";
		z-index: 0;
	}
	&__container {
		width: 100%;
	}

	&__blocks {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 50px;
		z-index: 11;
		@media (max-width: 992px) {
			gap: 20px;
		}

		@media (max-width: 768px) {
			flex-direction: column;
		}
	}

	&__info-block {
		display: flex;
		flex-direction: column;
		gap: 20px;
		@include descript;
		@media (max-width: 992px) {
			gap: 10px;
		}
	}

	&__title {
		font-size: 32px;
		font-weight: 200;
		margin-bottom: 20px;
		@media (max-width: 992px) {
			font-size: 25px;
		}
	}

	&__info {
		z-index: 99;
	}

	&__info-block {
	}
}

.benefits {
	background-color: var(--light-color);
	padding: 72px 40px;
	max-width: 546px;
	width: 100%;
	z-index: 99;
	display: flex;
	flex-direction: column;
	gap: 15px;
	p {
		@include descript;
	}

	@media (max-width: 992px) {
		gap: 5px;
		padding: 52px 20px;
	}
}

/* Наши эксперты section*/

.experts {
	padding: 55px 0;
	background-color: var(--accent-gold-color);
	@media (max-width: 668px) {
		padding: 20px 0;
	}
	&__container {
		width: 100%;
	}

	&__blocks {
		position: relative;
	}

	&__content {
		display: flex;
		flex-direction: column;
		text-align: center;
		margin: 0 auto;
	}

	&__title {
		position: relative;
		display: flex;
		flex-direction: column;
		margin: 0 auto;
		margin-bottom: 25px;
		max-width: 370px;
		width: 100%;
		@include title;
		&::before {
			position: absolute;
			bottom: -10px;
			left: 50%;
			transform: translateX(-50%);
			width: 80%;
			content: "";
			border-bottom: 3px solid;
			border-image: radial-gradient(circle, #717171 0%, #ece9e3 100%, #d1b496 100%);
			border-image-slice: 1;
		}
	}

	// Slider
}

.carousel {
	position: relative;
	margin-top: 20px;
	user-select: none;

	&__slide {
		overflow: hidden;
		border: none;
		@media (max-width: 400px) {
			display: flex;
			flex-direction: column;
			margin: 0 auto;
			align-items: center;
			justify-content: center;
			overflow: auto;
		}

		img {
			width: 100%;
			min-height: 255px;
			height: auto;
			max-width: 100%;
			object-fit: cover;
			margin-bottom: 15px;
			@media (max-width: 672px) {
				object-fit: contain;
				margin-bottom: -20px;
				overflow: visible;
			}

			@media (max-width: 620px) {
				margin-bottom: -2px;
			}

			@media (max-width: 620px) {
				margin-bottom: 15px;
			}

			@media (max-width: 400px) {
				width: 200px;
				height: 200px;
				margin-bottom: -15px;
			}

			@media (max-width: 480px) {
				margin-bottom: -25px;
			}
		}

		@media (max-width: 620px) {
			overflow: visible;
		}
	}

	&__blocks {
		margin: 0 0px 70px;
	}

	.swiper-btns {
		display: flex;
		justify-content: center;
		position: absolute;
		// top: 0;
		bottom: 12px;
		width: 35%;
		left: 50%;
		transform: translateX(-50%);
		z-index: 999;
		margin-bottom: 0px;
		@media (max-width: 1200px) {
			width: 40%;
		}
		@media (max-width: 992px) {
			width: 45%;
		}

		@media (max-width: 756px) {
			width: 65%;
		}
		@media (max-width: 576px) {
		}
	}

	.swiper-btns img {
		width: 30px;
		cursor: pointer;
		@media (max-width: 756px) {
			width: 25px;
		}
		@media (max-width: 576px) {
			display: none;
		}
	}

	&__content {
		h1 {
			font-size: 20px;
			color: var(--black-color);
			font-weight: 400;
		}

		p {
			@include descript;
		}
	}

	.swiper-pagination {
		display: flex;
		justify-content: center;
		gap: 16px;
	}

	.swiper-pagination-bullet {
		width: 30px;
		height: 3px;
		background-color: var(--disable-color);
		// margin: 0 15px;
		border-radius: 0px;
	}

	.swiper-pagination-bullet-active {
		background-color: var(--brown-color);
		width: 30px;
		height: 3px;
		border-radius: 0px;
		// border-radius: 0px;
	}
}
// descript

.descript {
	padding: 55px 0;
	@media (max-width: 678px) {
		padding: 30px 0;
	}
	&:nth-child(2n) {
		background-color: var(--silver-color);
	}
	&__container {
		width: 100%;
	}

	&__blocks {
		display: flex;
		align-items: center;
		gap: 133px;
		justify-content: center;
		@media (max-width: 992px) {
			gap: 50px;
		}

		@media (max-width: 768px) {
			justify-content: space-around;
			gap: 20px;
		}

		@media (max-width: 678px) {
			display: flex;
			flex-direction: column-reverse;
		}
	}

	&__blocks-reverse {
		flex-direction: row-reverse;
		@media (max-width: 992px) {
			gap: 50px;
		}

		@media (max-width: 768px) {
			justify-content: space-around;
			gap: 20px;
		}

		@media (max-width: 678px) {
			display: flex;
			flex-direction: column-reverse;
		}
	}

	&__content {
		max-width: 546px;
		width: 100%;
		display: flex;
		flex-direction: column;
		margin-bottom: 50px;
		@media (max-width: 992px) {
			max-width: 450px;
		}

		@media (max-width: 768px) {
			max-width: 350px;
			line-height: 120%;
		}

		@media (max-width: 678px) {
			max-width: 100%;
			text-align: center;
			margin-bottom: 20px;
		}
	}

	&__img {
		max-width: 255px;
		width: 100%;
		max-height: 350px;

		@media (max-width: 678px) {
			max-width: 200px;
			width: 100%;
			min-height: 200px;
			margin-bottom: 35px;
			margin-top: -20px;
			img {
				object-fit: cover;
			}
		}

		@media (max-width: 480px) {
			max-width: 180px;
			width: 100%;
			max-height: 180px;
			margin-bottom: 80px;
			margin-top: -10px;
			img {
				object-fit: cover;
			}
		}
	}

	&__title {
		position: relative;
		@include title;
		padding-bottom: 15px;
		max-width: 500px;
		width: 100%;
		font-size: 300;
		@media (max-width: 678px) {
			max-width: 100%;
		}
		&::before {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 90%;
			content: "";
			border-bottom: 3px solid;
			border-image: linear-gradient(to right, #717171 0%, #d1b496 50%, #ffffff 100%);
			border-image-slice: 1;

			@media (max-width: 678px) {
				max-width: 30px;
				left: 50%;
				transform: translateX(-50%);
				border-image: linear-gradient(to right, #717171 100%, #d1b496 60%, #f5f5f4);
				border-image-slice: 1;
			}
		}
	}

	&__desc {
		display: flex;
		flex-direction: column;
		gap: 15px;
		margin-top: 25px;
		@include descript;
		margin-bottom: 24px;
		@media (max-width: 768px) {
			gap: 10px;
		}
	}

	&__hello {
		font-weight: 400;
	}

	//
}

// partners

.partners {
	padding: 55px 0;
	background-color: var(--light-color);
	@media (max-width: 668px) {
		padding: 30px 0;
	}

	&__container {
		width: 100%;
	}

	&__blocks {
		display: flex;
		align-items: center;
		gap: 133px;
		justify-content: center;
		@media (max-width: 992px) {
			gap: 50px;
		}

		@media (max-width: 768px) {
			justify-content: space-around;
			gap: 20px;
		}

		@media (max-width: 678px) {
			display: flex;
			flex-direction: column-reverse;
		}

		@media (max-width: 678px) {
			margin: 0 auto;
			text-align: center;
		}
	}

	&__img {
		max-width: 345px;
		width: 100%;
		height: 360px;
		@media (max-width: 678px) {
			max-width: 245px;
			width: 100%;
			height: 260px;
		}
	}

	&__title {
		@include title;
		margin-bottom: 30px;
	}

	span {
		position: relative;
		font-size: 32px;
		font-weight: 200;
		padding-bottom: 14px;
		&::before {
			position: absolute;
			bottom: 5px;
			left: 0;
			width: 100%;
			content: "";
			border-bottom: 3px solid;
			border-image: linear-gradient(to right, #717171 0%, #d1b496 50%, #e7e1dd 100%);
			border-image-slice: 1;

			@media (max-width: 678px) {
				max-width: 30px;
				left: 50%;
				transform: translateX(-50%);
				border-image: linear-gradient(to right, #717171 100%, #d1b496 60%, #f5f5f4);
				border-image-slice: 1;
			}
		}
	}
}
