.calculator {
	padding: 130px 0 55px 0;
	background-color: var(--white-color);
	&__title {
		@include title;
		text-align: center;
		margin-top: 36px;
		margin-bottom: 50px;
	}

	/*  == == == == == == == == == ==  */

	&__title-form {
		@include addInfo;
		margin-bottom: 20px;
	}

	// -----------

	&__inputs {
		display: flex;
		align-items: center;
		gap: 30px;
		margin-bottom: 24px;
		@media (max-width: 400px) {
			gap: 15px;
		}
	}

	&__label {
		display: flex;
		flex-direction: column;
		@include titleUnder;
		max-width: 150px;
		width: 100%;
	}

	&__label-1 {
		max-width: 100px;
		width: 100%;
	}

	&__input {
		background-color: var(--silver-color);
		text-align: center;
	}

	&__input,
	&__select {
		padding: 5px 25px 5px 24px;
		font-size: 14px;
		margin-top: 5px;
	}

	.option {
		&:hover {
			background-color: var(--silver-color);
		}
	}

	&__select {
		background-color: var(--silver-color);
		&:focus {
			outline: none;
		}
	}

	&__buttons {
		display: flex;
		gap: 40px;
		text-transform: uppercase;
		margin-bottom: 63px;
		@media (max-width: 400px) {
			flex-direction: column-reverse;
			gap: 15px;
		}
	}

	&__button {
		border: 1px solid var(--black-color);
		max-width: 145px;
		padding: 12px 0;
		width: 100%;
		text-transform: uppercase;
		@include descript2;
		color: var(--black-color);
		text-align: center;
	}

	&__button--secondary {
		border: none;
		max-width: 270px;
		width: 100%;
		background-color: var(--light-color);
	}

	// -----------------

	&__result-wrap {
		display: flex;
		align-items: center;
		max-width: 900px;
		width: 100%;
		justify-content: space-between;
		@media (max-width: 1200px) {
			max-width: 600px;
		}
		@media (max-width: 768px) {
			flex-direction: column;
			align-items: start;
			margin-top: -50px;
			// gap: 70px;
		}
	}

	&__result {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__decor {
		position: absolute;
		content: "";
		height: 85px;
		width: 85px;
		left: 100px;
		background-image: url("./../images/calculate/01.svg");
		background-repeat: no-repeat;
		background-size: contain;
		@media (max-width: 992px) {
			height: 50px;
			width: 50px;
			left: 80px;
		}
	}

	&__decor-2 {
		position: absolute;
		content: "";
		height: 85px;
		width: 85px;
		left: 150px;
		background-image: url("./../images/calculate/01.svg");
		background-repeat: no-repeat;
		background-size: contain;
		@media (max-width: 992px) {
			height: 50px;
			width: 50px;
		}
	}

	&__results {
		position: relative;
		margin-top: 20px;
		display: flex;
		flex-direction: column;
		gap: 20px;
		max-width: 170px;
		width: 100%;
		&::before {
			position: absolute;
			right: -120px;
			top: 50%;
			transform: translateY(-40%);
			content: "";
			width: 100px;
			height: 100px;
			background-image: url("./../images/calculate/01.svg");
			background-repeat: no-repeat;
			background-size: cover;
		}

		@media (max-width: 400px) {
			// margin-top: 0;
			max-width: 120px;
		}
	}

	&__result {
		display: flex;
		justify-content: space-between;
	}

	&__circle {
		min-width: 53px;
		height: 53px;
		background-color: #f5f5dc;
		border-radius: 50%;
		border: 1px solid var(--line-color);
		@media (max-width: 992px) {
			min-width: 45px;
			height: 45px;
		}

		@media (max-width: 400px) {
			min-width: 35px;
			height: 35px;
		}
	}

	&__circle-result {
		position: absolute;
		top: 50%;
		left: 250px;
		transform: translateX(-50%);
		top: 50%;
		transform: translateY(-30%);
		min-width: 53px;
		height: 53px;
		background-color: #e7e1dd;
		border-radius: 50%;
		border: none;

		@media (max-width: 992px) {
			min-width: 45px;
			height: 45px;
		}

		@media (max-width: 400px) {
			min-width: 35px;
			height: 35px;
			left: 200px;
		}
	}

	// ==== ==== ==== ==== === ====

	&__picture {
		margin: 100px 0;
		@media (max-width: 768px) {
			margin: 50px 0;
		}
	}
	&__picture img {
		display: flex;
		margin: 0 auto;
		max-width: 1000px;
		width: 100%;
		height: 100%;
		object-fit: cover;
		@media (max-width: 1550px) {
			max-width: 800px;
		}

		@media (max-width: 1200px) {
			max-width: 650px;
		}

		@media (max-width: 768px) {
			max-width: 550px;
		}
	}
}

.chakra {
	&__table {
		width: 100%;
		border-collapse: collapse;
		text-align: left;
		margin-top: 20px;
		table-layout: auto;

		tr {
			width: 100%;
			border: none;
		}

		th,
		td {
			padding: 10px;
			border-bottom: 1px solid #ccc;
			text-align: left;
			white-space: nowrap;
		}

		th {
			background-color: transparent;
			font-weight: bold;
		}

		td:not(:last-child) {
			@include descript;
			font-weight: 500;
		}
	}

	&__chakra-name {
		display: flex;
		align-items: center;
		gap: 20px; // Зменшити розрив для мобільних пристроїв
		justify-content: space-between;
	}

	&__color {
		display: inline-block;
		width: 24px;
		height: 24px;
	}

	&__total {
		margin-top: 10px;
		text-align: right;
	}
}

// Медіа-запити для мобільних пристроїв
@media (max-width: 768px) {
	.chakra__table {
		font-size: 12px; // Зменшити шрифт
	}

	.chakra__chakra-name {
		gap: 10px; // Зменшити відступи
	}

	.chakra__table th,
	.chakra__table td {
		padding: 5px; // Зменшити відступи
	}
}
