@mixin title {
	font-weight: 200;
	font-size: 50px;
	line-height: 140%;
	@media (max-width: 992px) {
		font-size: 40px;
	}

	@media (max-width: 480px) {
		font-size: 25px;
		font-weight: 300;
	}
}

@mixin descript {
	font-size: 16px;
	line-height: 140%;
	@media (max-width: 992px) {
		font-size: 14px;
	}

	@media (max-width: 480px) {
		font-size: 12px;
	}
}

@mixin descript2 {
	font-size: 14px;
	line-height: 140%;
	@media (max-width: 992px) {
		font-size: 12px;
	}

	@media (max-width: 480px) {
		font-size: 10px;
	}
}

@mixin titleUnder {
	font-size: 24px;
	line-height: 140%;
	@media (max-width: 992px) {
		font-size: 20px;
	}

	@media (max-width: 480px) {
		font-size: 18px;
	}
}

@mixin titleUnder3 {
	font-size: 18px;
	line-height: 140%;
	@media (max-width: 992px) {
		font-size: 14px;
	}

	@media (max-width: 480px) {
		font-size: 12px;
	}
}

@mixin titleUnder2 {
	font-size: 20px;
	line-height: 140%;
	@media (max-width: 992px) {
		font-size: 18px;
	}

	@media (max-width: 480px) {
		font-size: 16px;
	}
}

@mixin btn {
	max-width: 256px;
	height: 55px;
	width: 100%;
	font-size: 16px;
	text-transform: uppercase;
	@media (max-width: 992px) {
		max-width: 200px;
		font-size: 14px;
	}

	@media (max-width: 480px) {
		max-width: 140px;
		height: 40px;
		font-size: 10px;
	}
}

@mixin addInfo {
	font-size: 32px;
	font-weight: 200;
	@media (max-width: 992px) {
		font-size: 25px;
	}

	@media (max-width: 680px) {
		font-size: 17px;
	}
}

@mixin decor {
	&::before {
		position: absolute;
		bottom: 5px;
		left: 0;
		// width: 80%;
		max-width: 370px;
		width: 100%;
		content: "";
		border-bottom: 3px solid;
		border-image: linear-gradient(to right, #717171 40%, #d1b496 60%, #f5f5f4);
		border-image-slice: 1;
		@media (max-width: 668px) {
			max-width: 30px;
			left: 50%;
			transform: translateX(-50%);
			border-image: linear-gradient(to right, #717171 100%, #d1b496 60%, #f5f5f4);
			border-image-slice: 1;
		}
	}
}

@mixin titlProNacc {
	@media (max-width: 992px) {
		font-size: 60px;
	}
}

@mixin basketContent {
	font-size: 12px;
	line-height: 140%;
}
