#navbar {
	position: fixed;
	top: 0;
	z-index: 9999;
	display: flex;
	transition: top 0.3s ease;
	width: 100%;
}

.header {
	position: fixed;
	top: 0;
	left: 0;
	min-height: 75px;
	width: 100%;
	background-color: var(--black-color);
	display: flex;
	align-items: center;
	justify-content: space-between;
	z-index: 999;

	&__container {
		width: 100%;
	}

	&__blocks {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		z-index: 999;
	}

	&__burger-menu {
		display: none;
		z-index: 9999;

		@media (max-width: 992px) {
			display: block;
			z-index: 9999;
		}
	}
}

.menu {
	min-height: 75px;
	display: flex;
	align-items: center;

	&__list {
		display: flex;
		align-items: center;
		gap: 40px;
		text-align: center;
		height: 100%;
		@media (max-width: 1200px) {
			gap: 25px;
		}

		@media (max-width: 992px) {
			display: block;
			text-align: left;
			padding: 70px 20px 20px 20px;
			width: 100%;
		}
	}

	&__item {
		position: relative;
		min-height: 75px;
		display: flex;
		align-items: center;
		text-transform: uppercase;

		&:hover .menu__link {
			color: var(--gold-color);
			transition: color 0.3s ease;
			i {
				transform: rotate(180deg);
				transition: transform 0.3s ease-in-out;
			}
		}

		i {
			display: none;
			font-size: 15px;
		}
	}

	&__link {
		font-size: 14px;
		display: flex;
		gap: 5px;
		align-items: center;
		color: var(--white-color);
		&.active-page {
			color: var(--gold-color);
		}
	}

	&__sub-list {
		position: absolute;
		left: 0;
		top: 50px;
		background-color: var(--black-color);
		text-align: left;
		padding: 25px 40px;
		opacity: 0;
		visibility: hidden;
		transform: translateY(-10px);
		z-index: 9;
		transition: all 0.3s ease;
		display: flex;
		flex-direction: column;
		gap: 10px;
		font-size: 14px;
		text-transform: uppercase;
		white-space: nowrap;
		transform: translate(0, 5px);
		&.active2 {
			opacity: 1;
			visibility: visible;
			transform: translate(0px, 0);
		}
	}

	&__sub-list li:last-child() {
		border: none;
	}

	&__sub-link {
		color: var(--white-color);

		&:hover,
		&.active {
			color: var(--gold-color);
		}
	}
}

.languages {
	@media (max-width: 992px) {
		display: none;
	}

	&__blocks {
		display: flex;
		align-items: center;
	}

	&__block {
		padding-left: 8px;
		color: var(--white-color);
		cursor: pointer;

		&:not(:last-child) {
			border-right: 1px solid rgb(255, 255, 255);
			padding-right: 8px;
		}

		&.active {
			color: var(--gold-color);
		}
	}
}

.info {
	display: flex;
	align-items: center;

	&__list {
		display: flex;
		list-style: none;
		padding: 0;
		margin: 0;
	}

	&__item {
		position: relative;
		margin-left: 20px;

		img {
			width: 24px;
			height: 24px;
			transition: transform 0.3s ease;
		}
	}

	&__sub-link {
		color: var(--white-color);
		&:hover {
			color: var(--gold-color);
		}
	}

	&__sub-list {
		position: absolute;
		right: 0px;
		top: 50px;
		background-color: var(--black-color);
		text-align: left;
		padding: 25px 40px;
		opacity: 0;
		visibility: hidden;
		transform: translateY(-10px);
		z-index: 9;
		transition: all 0.3s ease;
		display: flex;
		flex-direction: column;
		gap: 10px;
		font-size: 14px;
		text-transform: uppercase;
		white-space: nowrap;
		transform: translate(0, 5px);
		&.active {
			opacity: 1;
			visibility: visible;
			transform: translate(0px, 0);

			// @media (max-width: 992px) {
			// 	display: none;
			// }
		}
	}
}

#nav-icon3 {
	width: 35px;
	height: 25px;
	position: relative;
	transform: rotate(0deg);
	transition: 0.5s ease-in-out;
	cursor: pointer;

	span {
		display: block;
		position: absolute;
		height: 2px;
		width: 100%;
		background: var(--white-color);
		border-radius: 9px;
		opacity: 1;
		left: 0;
		transition: 0.25s ease-in-out;
	}

	span:nth-child(1) {
		top: 0px;
	}

	span:nth-child(2),
	span:nth-child(3) {
		top: 8px;
	}

	span:nth-child(4) {
		top: 16px;
	}

	&.open span:nth-child(1) {
		top: 0px;
		width: 0%;
		left: 50%;
	}

	&.open span:nth-child(2) {
		transform: rotate(45deg);
	}

	&.open span:nth-child(3) {
		transform: rotate(-45deg);
	}

	&.open span:nth-child(4) {
		top: 10px;
		width: 0%;
		left: 50%;
	}
}

@media (max-width: 992px) {
	.header {
		z-index: 99999;
		position: fixed;
		transition: all 0.3s ease;

		&__logo {
			display: block;
			z-index: 99999;
		}
	}

	.menu {
		position: fixed;
		right: 0;
		top: 68px;
		width: 100%;
		height: 100%;
		background-color: var(--black-color);
		overflow-y: auto;
		z-index: 9999;
		transform: translateX(-100%);
		transition: all 0.3s ease;
		display: block;

		&__list {
			margin-top: -50px;
			transition: all 0.3s ease;
			margin-bottom: 100px;
			overflow-y: auto;
		}

		&__item {
			display: flex;
			flex-direction: column;
			transition: all 0.3s ease;

			i {
				display: block;
			}
		}

		&.active {
			transform: translateX(0);
			box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
			transition: all 0.3s ease;
		}

		li {
			border-bottom: 1px solid #f6f6f62e;
			padding: 30px 0;
			transition: all 0.3s ease;

			a {
				display: flex;
				justify-content: space-between;
				width: 100%;
			}
		}

		&__sub-list {
			position: static;
			opacity: 0;
			height: 0;
			transform: translateY(-20px);
			visibility: hidden;
			padding: 0;
			width: 100%;
			overflow: hidden;
			margin-bottom: -25px;
			padding-top: 5px;
			background-color: transparent;
			transition: height 0.3s ease, opacity 0.3s ease, transform 0.3s ease;

			&.active {
				height: 100%;
				opacity: 1;
				visibility: visible;
				transform: translateY(0);
				// transform: translateY(0); /* Зворотний рух */
				transition: opacity 0.3s ease, transform 0.3s ease;
			}

			li {
				border-bottom: 1px solid #f6f6f62e;
				padding: 15px 0;
				line-height: 134%;
				transition: all 0.3s ease;
				&:last-child {
					border-bottom: none; /* Прибираємо border у останнього елемента */
				}
			}

			a {
				color: #ffffff80;
			}
		}
	}
}
