.market {
	padding: 130px 0 55px 0;
	min-height: 100vh;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	background-image: url("./../images/Market/market-bg.png");
	background-image: url("./../images/Market/market-bg.webp");

	background-attachment: fixed;
	@media (max-width: 992px) {
		// background-position: bottom right;
	}

	&__header {
		padding-top: 70px;
	}

	&__title {
		@include title;
		margin-bottom: 20px;
		color: #764631;
		font-family: "Poiret", sans-serif;
		text-transform: uppercase;
		font-size: 56px;
	}

	&__descript {
		max-width: 700px;
		width: 100%;
		font-family: "Clasica", sans-serif;
		// font-size: 25px;
		color: var(--black-color);
		line-height: 140%;
		font-weight: 500;
		@include titleUnder;
	}
}

.category {
	padding: 32px 0;

	&__buttons {
		display: flex;
		align-items: center;
		justify-content: space-between;

		gap: 33px;
		h1 {
			@include descript;
			font-weight: 400;
			color: var(--black-color);
			text-transform: uppercase;
		}
	}

	&__buttons {
		display: flex;
		margin-bottom: 70px;

		@media (max-width: 1100px) {
			flex-direction: column;
			justify-content: center;
			align-items: center;
			margin: 0 auto;
			text-align: center;
			margin-bottom: 40px;
		}
	}

	.category__buttons-btn a {
		@media (max-width: 866px) {
			display: flex;
			gap: 20px;
		}
	}

	.category-btn {
		padding: 10px 30px;
		@include descript;
		color: var(--black-color);
		transition: 0.3s ease-in-out;
		@media (max-width: 866px) {
		}
	}

	.category__options-drop {
		max-height: 0;
		overflow: hidden;
		transition: max-height 0.3s ease-out;
	}

	.category__options-drop.active {
		max-height: 500px;
	}

	.popular img {
		transition: transform 0.3s ease;
	}

	.popular img.rotate {
		transform: rotate(180deg);
	}

	// category____descript

	&__descript {
		display: flex;
		flex-direction: column;
		gap: 16px;
		padding: 0 0 40px 0;
		display: none;
		&.active {
			display: block;
		}
		h1 {
			@include title;
		}

		p {
			max-width: 740px;
			width: 100%;
			@include titleUnder2;
		}
	}

	// category__options

	&__options {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@media (max-width: 730px) {
			gap: 16px;
			flex-direction: column;
		}
		h1 {
			@include descript;
			font-weight: 400;
			text-transform: uppercase;
		}
	}

	&__options-list {
		position: relative;
		display: flex;
		align-items: center;
		gap: 16px;
		margin-bottom: 25px;
		a {
			display: flex;
			gap: 16px;
			align-items: center;
			color: var(--black-color);
			@include descript2;
			color: var(--grey-color);
			text-transform: uppercase;
			margin: 0;
			padding: 0;
			border: 1px solid #a99788;
			transition: all 0.3s ease-in-out;
			padding: 14px;
			&.active {
				background-color: var(--accent-pink-color);
				color: var(--white-color);
				border: none;
			}
		}

		// .poular a {
		// 	display: none;
		// }

		.category__options-drop {
			position: absolute;
			top: 55px;
			padding: 16px 64px 16px 41px;
			background-color: var(--accent-gold-color);
			display: flex;
			flex-direction: column;
			gap: 8px;
			max-height: 0;
			overflow: hidden;
			opacity: 0;
			visibility: hidden;
			z-index: 99;
			transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out, max-height 0.4s ease-in-out, visibility 0.4s;
			z-index: 9999;
			text-transform: capitalize;

			a.popular {
				display: none;
			}

			li {
				@include descript;
				font-weight: 200;
				position: relative;
				cursor: pointer;
				text-transform: capitalize;
				a {
					color: var(--black-color);
					text-transform: capitalize;
					@include descript;
				}
			}

			&.active {
				max-height: 500px;
				opacity: 1;
				visibility: visible;
			}
		}

		.category__options-drop.category__options-drop-last {
			right: 0;
		}
		.category__options-drop-item a {
			border: none;
			padding: 0;
			margin: 0;
			&.active {
				background-color: var(--accent-pink-color);
				color: var(--white-color);
				border: none;
				display: none;
			}
		}

		@media (max-width: 500px) {
			gap: 10px;
		}

		@media (max-width: 400px) {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
		}
	}

	.filter {
		padding: 12px 55px;
		@media (max-width: 500px) {
			padding: 12px 40px;
		}
	}

	.popular {
		padding: 12px 32px;
		@media (max-width: 500px) {
			padding: 12px 25px;
		}
	}

	.menu-market {
		padding: 13px 14px;
	}

	// category__products

	&__products-item {
		position: relative;
		max-width: 255px;
		width: 100%;
		height: 255px;
		border: 1px solid var(--line-color);
		a {
			color: var(--black-color);

			img {
				position: relative;
				height: 170px;
				// background-color: #c1c1c1;
				width: 100%;
				object-fit: cover;
				&::after {
					position: absolute;
					background-image: url("./../images/Market/product-example.webp");
					background-repeat: no-repeat;
					background-size: contain;
					width: 100px;
					height: 100px;
					content: "";
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
				}
			}
		}
	}

	.buy {
		position: absolute;
		right: 12px;
		top: 12px;
		z-index: 99;
		width: 34px;
		height: 34px;
		content: "";
		background-image: url("./../images/Market/close-market.svg");
		background-repeat: no-repeat;
		background-size: contain;
		cursor: pointer;
	}

	&__content {
		padding: 0px 16px 0px 16px;
		display: flex;
		flex-direction: column;
		text-align: center;
		h1 {
			@include descript;
			font-weight: 400;
		}

		p {
			@include descript;
			font-weight: 200;
		}

		span {
			@include titleUnder2;
			font-weight: 400;
		}
	}

	&__products-list {
		display: grid;
		gap: 36px;
		grid-template-columns: repeat(4, 1fr);
		justify-items: center;
		@media (max-width: 992px) {
			gap: 20px;
		}

		@media (max-width: 850px) {
			grid-template-columns: repeat(3, 1fr);
		}
		@media (max-width: 640px) {
			gap: 10px;
		}

		@media (max-width: 568px) {
			grid-template-columns: repeat(2, 1fr);
		}

		@media (max-width: 400px) {
			grid-template-columns: repeat(1, 1fr);
			gap: 15px;
		}
	}

	/* -- - - -- - - - -- - */

	&__descript {
		&.hide {
			display: none;
		}
	}

	.filter__buttons a {
		&.active {
			background-color: var(--accent-rose-color);
			color: var(--white-color);
		}
	}
}

.category__products-item {
	display: none;
}

.category__products-item.active {
	display: block;
}

.category__descript {
	display: none;
}

.category__descript.active {
	display: block;
}

.heart-button {
	position: absolute;
	top: 10px;
	left: 10px;
	background: none;
	border: none;
	cursor: pointer;
	display: inline-block;
}

.heart-button .heart {
	fill: transparent;
	stroke: var(--black-color);
	stroke-width: 1;
	transition: fill 0.3s ease, stroke 0.3s ease;
}

.heart-button.active .heart {
	fill: #f18485;
	stroke: #f18485;
}
