.order-empty {
	padding: 133px 0 55px 0;
	&__content {
		margin-top: 100px;
		text-align: center;
		img {
			object-fit: contain;
			width: 150px;
			height: 140px;
		}

		h1 {
			@include addInfo;
			color: var(--black-color);
			font-weight: 400;
			margin-top: 16px;
			margin-bottom: 8px;
		}

		p {
			@include titleUnder2;
			margin-bottom: 32px;
			font-weight: 400;
		}
	}

	&__btn {
		text-transform: uppercase;
		color: var(--white-color);
		max-width: 255px;
		width: 100%;
		padding: 16px;
		background-color: var(--accent-pink-color);
		display: flex;
		margin: 0 auto;
		justify-content: center;
		margin-bottom: 130px;
		@include btn;
		a {
			color: var(--white-color);
		}
	}
}

/* ======================== */

.order-history {
	padding: 133px 0 55px 0;
	&__content {
		display: flex;
		flex-direction: column;
		gap: 40px;
		margin-bottom: 112px;
	}
}

.order-card {
	display: flex;
	flex-direction: column;
	width: 100%;
	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		background-color: var(--silver-color);
		padding: 24px 28px;
		@media (max-width: 792px) {
			padding: 24px 28px;
		}
		div {
			display: flex;
			flex-direction: column;
			gap: 4px;
		}
	}

	&__number {
		color: var(--brown-color);
		@include basketContent;
		font-weight: 400;
	}

	&__status {
		@include descript;
		font-weight: 400;
	}

	&__date {
		color: var(--grey-color);
		font-size: 14px;
	}

	.order-card__delivered {
		display: flex;
		flex-direction: row;
		align-items: center;
		gap: 8px;
	}

	&__design {
		width: 8px;
		height: 8px;
		border-radius: 50%;
		background-color: #32ac37;
	}
	&__design-2 {
		width: 8px;
		height: 8px;
		border-radius: 50%;
		background-color: #f18485;
	}

	&__button {
		max-width: 160px;
		width: 100%;
		padding: 12px 0px;
		justify-content: center;
		text-align: center;
		background-color: var(--accent-pink-color);
		text-transform: uppercase;
		@include descript2;
		a {
			color: var(--white-color);
		}
		@media (max-width: 480px) {
			max-width: 120px;
		}
	}

	&__body {
		display: flex;
		border: 1px solid var(--line-color);
		border-top: none;
		gap: 200px;
		padding: 24px 12px 24px 28px;
		@media (max-width: 992px) {
			justify-content: space-between;
			gap: 0px;
		}

		@media (max-width: 654px) {
			flex-direction: column;
			gap: 15px;
		}
	}

	&__left {
		display: flex;
		gap: 32px;
		img {
			width: 80px;
			height: 80px;
			object-fit: cover;
		}
		@media (max-width: 750px) {
			gap: 15px;
			// flex-direction: column;
		}
	}

	&__product {
		div {
			display: flex;
			gap: 16px;
			@include descript;
			@media (max-width: 654px) {
				flex-direction: column;
				gap: 5px;
			}
			h1 {
				text-transform: uppercase;
				font-weight: 500;
			}
			p {
				font-weight: 300;
			}
		}

		p {
			font-weight: 400;
		}
	}

	&__info p,
	&__info span {
		@include descript;
	}

	&__right {
		max-width: 415px;
		width: 100%;

		@media (max-width: 740px) {
			max-width: 300px;
		}
	}
	&__info {
		display: flex;
		flex-direction: column;
		gap: 16px;
		align-items: end;
		// background-color: #32ac37;
		div {
			display: flex;
		}
	}

	&__wrap {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
		text-align: left;
		max-width: 415px;
		width: 100%;
		@media (max-width: 792px) {
			max-width: 315px;
		}
		p {
			color: #737373;
		}
		// background-color: #32ac37;
	}
}

/* ======================== */

// addInfo

.reviewed {
	&__title {
		@include addInfo;
		font-weight: 400;
		margin-bottom: 40px;
	}

	&__blocks {
		display: flex;
		// gap: 30px;
	}

	&__block {
		border: 1px solid var(--line-color);
		padding: 16px 0 8px 0;
		text-align: center;
		max-width: 255px;
		width: 100%;
		gap: 32px;
		@media (max-width: 792px) {
			gap: 15px;
		}

		img {
			width: 145px;
			height: 150px;
			object-fit: contain;
			margin-bottom: 5px;
		}

		h1 {
			@include descript;
			font-weight: 500;
		}

		span {
			@include descript;
			font-weight: 200;
		}

		p {
			font-weight: 400;
			@include titleUnder2;
		}
	}

	.swiper-pagination {
		display: flex;
		position: absolute;
		justify-content: center;
		gap: 16px;
	}

	.swiper-pagination-bullet {
		width: 30px;
		height: 3px;
		background-color: var(--disable-color);
		border-radius: 0px;
	}

	.swiper-pagination-bullet-active {
		background-color: var(--brown-color);
		width: 30px;
		height: 3px;
		border-radius: 0px;
	}

	.swiper-wrapper {
		margin-bottom: 80px;
	}
}
