// Footer

.footer {
	background-color: var(--black-color);
	padding: 55px 0;
	color: var(--white-color);
	z-index: 999;
	&__container {
		width: 100%;
	}
	// @media (max-width: 668px) {
	// 	padding: 30px 0;
	// }

	&__blocks {
		display: flex;
		justify-content: space-between;
		gap: 20px;
		@media (max-width: 768px) {
			flex-direction: column;
			align-items: center;
			margin: 0 auto;
			justify-content: center;
			gap: 60px;
			width: 100%;
		}
	}

	&__blocks-wrapper {
		display: flex;
		gap: 100px;
		@media (max-width: 992px) {
			gap: 50px;
		}
		@media (max-width: 768px) {
			width: 100%;
			justify-content: space-around;
		}
	}

	&__menu-list {
		display: flex;
		flex-direction: column;
		gap: 13px;
		@media (max-width: 992px) {
			gap: 10px;
		}
	}

	&__menu-item a {
		text-transform: uppercase;
		transition: color 0.2s ease-in-out;
		color: var(--white-color);
		font-size: 14px;
		@media (max-width: 992px) {
			font-size: 12px;
		}
		&:hover {
			color: var(--gold-color);
		}
	}

	&__social {
		display: flex;
		gap: 16px;
	}

	// __info

	&__info {
		h1 {
			@include descript;
			text-transform: uppercase;
			margin-bottom: 15px;
		}
	}

	&__info-about {
		display: flex;
		flex-direction: column;
		gap: 18px;

		a {
			display: flex;
			align-items: center;
			gap: 18px;
			cursor: pointer;

			p {
				@include descript;
				color: var(--white-color);
				transition: color 0.2s ease-in-out;

				&:hover {
					color: var(--gold-color);
				}
			}
		}

		img {
			@media (max-width: 768px) {
				width: 20px;
				height: 20px;
			}
		}
	}

	// Footer__below

	&__below-block {
		width: 100%;
		border-bottom: 1px solid var(--line-color);
	}

	&__below {
		margin-top: 36px;
		display: flex;
		flex-direction: column;
		p {
			margin-top: 24px;
			text-align: center;
			@include titleUnder2;
		}
	}
}

.footer__form {
	display: flex;
	flex-direction: column;
	gap: 20px;
	max-width: 350px;
	width: 100%;
	@media (max-width: 992px) {
		max-width: 300px;
	}

	h1 {
		@include descript;
		font-size: 18px;
		font-weight: 300;
	}

	.footer-btn {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-top: 20px;
		background-color: var(--white-color);
		color: var(--black-color);
		margin-bottom: 10px;
	}

	img {
		width: 30px;
		height: 30px;
		@media (max-width: 768px) {
			width: 30px;
			height: 30px;
		}
	}
}

.form {
	display: flex;
	flex-direction: column;
	gap: 20px;

	input {
		border-bottom: 1px solid var(--line-color);
		padding: 5px 15px 0 10px;
		padding-bottom: 10px;

		&::placeholder {
			color: var(--white-color);
			opacity: 0.7;
			font-weight: 200;
		}
	}
}
