.pythagoras {
	padding: 56px 0;
	background-color: var(--white-color);

	@media (max-width: 668px) {
		padding: 30px 0;
	}

	&__title {
		@include title;
		text-align: center;
		font-weight: 400;
		color: var(--accent-pink-color);
		margin-bottom: 36px;
	}

	&__blocks {
		display: grid;
		grid-template-columns: 1fr 2fr;
		gap: 50px;
		align-items: center;
		@media (max-width: 960px) {
			gap: 30px;
		}

		@media (max-width: 872px) {
			grid-template-columns: 1fr;
			gap: 30px;
		}
	}

	&__left {
		&-birthday {
			width: 70%;
			padding: 5px 0;
			text-align: center;
			font-weight: 500;
			color: var(--grey-color);
			border: 2px solid #d97706b6;
			border-left: none;
			margin-bottom: 30px;
			@media (max-width: 872px) {
				width: 50%;
			}
		}
	}

	&__tips {
		display: flex;
		flex-direction: column;
		gap: 25px;
		font-weight: 400;
		padding: 0 10px;
		@media (max-width: 872px) {
			justify-content: space-around;
			width: 70%;
			margin: 0 auto;
		}

		@media (max-width: 480px) {
			width: 100%;
		}

		&-block {
			display: flex;
			justify-content: space-between;
			align-items: center;
			gap: 10px;

			h1 {
				max-width: 150px;
				width: 100%;
				@include descript;
			}

			div {
				display: flex;
				flex-direction: column;
				text-align: center;

				p {
					@include descript;
					font-weight: 600;
					color: var(--grey-color);
				}

				span {
					color: var(--gold-color);
					@include descript;
				}

				h2 {
					color: var(--accent-pink-color);
					@include descript;
				}
			}
		}
	}

	&__form {
		display: grid;
		grid-template-columns: 2.5fr 0.75fr;
		gap: 30px;
		justify-content: space-between;
		justify-items: stretch;
		font-weight: 500;
		margin-bottom: 20px;
		@include descript;
		@media (max-width: 960px) {
			gap: 20px;
		}

		@media (max-width: 500px) {
			gap: 10px;
		}

		@media (max-width: 400px) {
			gap: 5px;
			margin-bottom: 10px;
		}
	}

	&__right-label {
		border: 10px solid #ece9e3;
		padding: 5px;
		@media (max-width: 960px) {
			border: 7px solid #ece9e3;
		}

		@media (max-width: 500px) {
			border: 3px solid #ece9e3;
			padding: 5px;
		}
	}

	.pythagoras__right-label-gold {
		border: 10px solid #d9770658;
		// padding: 5px;

		@media (max-width: 500px) {
			border: 3px solid #d9770658;
			padding: 5px;
		}
	}

	&__right {
		&-blocks {
			display: grid;
			grid-template-columns: repeat(4, 1fr);
			grid-template-rows: repeat(4, 135px);
			row-gap: 20px;
			column-gap: 20px;
			justify-items: stretch;
			align-items: stretch;
			@media (max-width: 960px) {
				row-gap: 15px;
				column-gap: 15px;
			}

			@media (max-width: 576px) {
				row-gap: 10px;
				column-gap: 10px;
			}

			@media (max-width: 480px) {
				row-gap: 5px;
				column-gap: 5px;
			}
			@media (max-width: 400px) {
				grid-template-rows: repeat(4, 100px);
			}

			div {
				position: relative;
				display: flex;
				flex-direction: column;
				font-weight: 500;
				line-height: 100%;
				@include descript;

				span {
					position: absolute;
					top: 20px;
					left: 5px;
					display: flex;
					width: 100%;
					text-align: left;
					@include titleUnder2;
					@media (max-width: 400px) {
						top: 0px;
					}
				}
			}
		}

		&-label {
			display: flex;
			flex-direction: column;
			text-align: left;
		}

		&-block {
			position: relative;
			border: 10px solid #ece9e3;
			display: flex;
			text-align: center;
			gap: 8px;
			align-items: center;
			justify-content: center;
			width: 100%;
			height: 100%;
			flex: 1;
			padding: 10px 5px;
			@include descript;
			cursor: pointer;
			@media (max-width: 960px) {
				border: 7px solid #ece9e3;
			}

			@media (max-width: 500px) {
				border: 3px solid #ece9e3;
				padding: 5px;
				gap: 5px;
			}

			@media (max-width: 370px) {
				border: 1px solid #ece9e3;
				padding: 3px;
				gap: 0px;
			}
			h1 {
				position: absolute;
				bottom: 5px;
			}
		}

		&__blocks-birthday {
			width: 100%;
		}
	}

	.birthday {
		display: flex;
		flex-direction: column;
		gap: 30px;
		width: 100%;
		margin: 0 auto;
		justify-content: center;
		align-items: center;
		margin-top: 56px;
		@media (max-width: 580px) {
			gap: 15px;
		}
		@media (max-width: 570px) {
			margin-top: 20px;
		}
		&__block {
			display: flex;
			align-items: center;
			gap: 20px;
			@include descript;
			@media (max-width: 570px) {
				margin-top: 36px;
			}
			@media (max-width: 580px) {
				gap: 10px;
				flex-direction: column;
			}
			h1 {
				text-transform: uppercase;
				@include titleUnder2;
				font-weight: 400;
			}

			span {
				background-color: #bcbcbc;
				padding: 10px 15px;
				color: var(--white-color);
				@media (max-width: 580px) {
					padding: 8px 12px;
				}
			}
		}
	}

	/* Вимикаємо календар для дати */
	input[type="date"]::-webkit-calendar-picker-indicator {
		display: none;
	}

	/* Стилі для вводу чисел */
	input[type="number"]::-webkit-inner-spin-button,
	input[type="number"]::-webkit-outer-spin-button {
		-webkit-appearance: none;
		margin: 0;
	}

	input[type="number"] {
		-moz-appearance: textfield;
		appearance: textfield;
	}
}
