.response {
	position: relative;
	padding: 130px 0 55px 0;
	background-color: var(--white-color);
	&__title {
		@include addInfo;
		text-align: left;
	}

	&__stars {
		display: flex;
		gap: 8px;
		margin-bottom: 20px;
		font-size: 40px;
	}

	&__star {
		font-size: 50px;
		color: transparent;
		text-align: center;
		transition: color 0.3s, -webkit-text-stroke 0.3s;
		cursor: pointer;
		-webkit-text-stroke: 1px #fedfc1;
		&.active {
			color: var(--gold-color);
			-webkit-text-stroke: 1px var(--gold-color);
		}
	}

	&__form {
		display: flex;
		flex-direction: column;
		gap: 15px;
	}

	&__input,
	&__textarea {
		width: 100%;
		padding: 10px;
		@include descript;
		border: 1px solid var(--line-color);
		border-radius: 5px;

		&:focus {
			border-color: #ffc78f;
			outline: none;
		}

		&::placeholder {
			@include descript;
			color: var(--line-color);
		}
	}

	&__textarea {
		height: 100px;
		resize: none;
	}

	&__checkbox {
		display: flex;
		align-items: center;
		font-size: 0.9rem;

		input {
			margin-right: 10px;
			height: 25px;
			width: 25px;
		}
	}

	&__button {
		display: inline-block;
		text-align: center;
		padding: 15px;
		max-width: 255px;
		width: 100%;
		font-size: 1rem;
		color: #fff;
		background-color: var(--accent-rose-color);
		border: none;
		border-radius: 5px;
		cursor: pointer;
		transition: background-color 0.3s;
		text-transform: uppercase;

		&:hover {
			background-color: var(--accent-pink-color);
		}
	}

	// мОДАЛЬНОЕ

	&__send {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		background-color: white;
		max-width: 700px;
		width: 100%;
		height: 400px;
		box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
		@media (max-width: 992px) {
			max-width: 500px;
			height: 300px;
		}

		@media (max-width: 556px) {
			max-width: 300px;
			height: 300px;
		}

		&-content {
			display: flex;
			flex-direction: column;
			align-items: center;
			text-align: center;
			padding-top: 100px;
			h1 {
				font-size: 30px;
				font-weight: 500;
				margin-bottom: 34px;
				@media (max-width: 992px) {
					font-size: 25px;
				}

				@media (max-width: 556px) {
					font-size: 20px;
				}
			}

			a {
				max-width: 255px;
				width: 100%;
				background-color: var(--accent-pink-color);
				color: white;
				@include descript;
				text-transform: uppercase;
				padding: 15px;

				@media (max-width: 556px) {
					font-size: 15px;
				}
			}
		}
	}

	&__admin {
		margin-top: 36px;
	}
	.response__wrap {
		display: flex;
		align-items: center;
		gap: 20px;
		img {
			object-fit: contain;
			width: 100px;
			height: 100px;
		}
	}
	.response__descript {
		display: flex;
		flex-direction: column;
		text-align: left;
		gap: 0px;
		margin: 0;
		padding: 0;
		&-h1 {
			@include descript;
		}

		&-p {
			font-size: 13px;
		}

		&-a {
			font-size: 15px;
			color: var(--grey-color);
			text-decoration: solid;
			margin: 0;
			padding: 0;
		}
	}
}
