.favorites {
	padding: 130px 0 55px 0;

	&__title {
		text-align: center;
		@include title;
		margin-top: 45px;
		margin-bottom: 56px;
		@media (max-width: 480px) {
			margin-bottom: 30px;
		}
	}

	.heart-button {
		position: absolute;
		top: 10px;
		left: 10px;
		background: none;
		border: none;
		cursor: pointer;
		display: inline-block;
		fill: #f18485;
		stroke: #f18485;
	}

	.heart-button .heart {
		fill: #f18485;
		stroke: #f18485;
	}

	.heart-button.active .heart {
		fill: transparent;
		stroke: var(--black-color);
		stroke-width: 1;
		transition: fill 0.3s ease, stroke 0.3s ease;
	}

	&__sort-list {
		position: relative;
		display: flex;
		justify-content: space-between;
		margin-bottom: 25px;
		margin-top: 56px;
		h1 {
			@include descript;
			font-weight: 400;
			text-transform: uppercase;
		}

		@media (max-width: 560px) {
			flex-direction: column;
			gap: 10px;
			justify-content: center;
			align-items: center;
		}
	}

	&__sort-button {
		position: relative;
		border: 1px solid var(--line-color);
		text-transform: uppercase;
		@include descript2;
		margin-bottom: 8px;
		display: flex;
		align-items: center;
		transition: transform 0.3s ease-in-out;
		max-width: 175px;
		width: 100%;
		padding: 12px 0;
		display: flex;
		align-items: center;
		justify-content: center;
		padding-right: 20px;
	}

	&__sorts-item {
		margin-bottom: 24px;
	}

	&-arrow {
		position: absolute;
		content: "";
		right: 32px;
		top: 50%;
		transform: translateY(-45%);
		width: 12px;
		height: 12px;
		background-image: url("./../images/Favorites-page/arrow.svg");
		background-repeat: no-repeat;
		background-size: contain;
		transform-origin: center;
		transition: transform 0.3s ease-in-out;
		&.active {
			transform: rotate(-180deg);
			transform: translateY(-70%) rotate(-180deg);
		}
	}

	// =======================
	&__sort-menu {
		position: absolute;
		padding: 16px 64px 16px 41px;
		background-color: var(--accent-gold-color);
		display: flex;
		flex-direction: column;
		gap: 8px;
		max-height: 0;
		overflow: hidden;
		opacity: 0;
		visibility: hidden;
		z-index: 99;
		transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out, max-height 0.4s ease-in-out, visibility 0.4s;

		li {
			@include descript;
			font-weight: 200;
			position: relative;
			cursor: pointer;
			a {
				color: var(--black-color);
			}
		}

		&.active {
			max-height: 500px;
			opacity: 1;
			visibility: visible;
		}
	}

	.favorites__products-link {
		position: relative;
		// display: block;
		// width: 100%;
		// height: 100%;
	}

	.favorites__products-list {
		@media (max-width: 920px) {
			gap: 20px;
		}
		@media (max-width: 880px) {
			gap: 20px;
			grid-template-columns: repeat(3, 1fr);
		}

		@media (max-width: 620px) {
			gap: 10px;
			row-gap: 30px;
			grid-template-columns: repeat(2, 1fr);
		}

		@media (max-width: 390px) {
			gap: 15px;
			column-gap: 70px;
			grid-template-columns: repeat(1, 1fr);
			justify-items: center;
		}
	}

	.favorites__products-item {
		display: flex;
		flex-direction: column;
		row-gap: 30px;
		margin-bottom: 50px;
	}

	.favorites-btn {
		width: 100%;
		padding: 12px;
		border: 1px solid var(--line-color);
		text-transform: uppercase;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&__heart {
		position: absolute;
		content: "";
		left: 16px;
		top: 16px;
		width: 25px;
		height: 25px;
		background-image: url("./../images/Favorites-page/heart.svg");
		background-repeat: no-repeat;
		background-size: contain;
	}

	.category__products-item {
		&::after {
			display: none;
		}
		&::before {
			display: none;
		}
	}

	.favorites__close {
		position: absolute;
		content: "";
		right: 16px;
		top: 16px;
		width: 20px;
		height: 20px;
		background-image: url("./../images/Favorites-page/close.svg");
		background-repeat: no-repeat;
		background-size: contain;
		display: block;
	}
}
