.single-product {
	position: relative;
	padding: 130px 0 55px 0;
	overflow: hidden;

	@media (max-width: 668px) {
		padding: 130px 0 55px 0;
	}

	&__content {
		display: flex;
		flex-direction: column;
		gap: 40px;
		max-width: 450px;
		width: 100%;

		h1 {
			@include addInfo;
			font-weight: 400;

			span {
				font-weight: 200;
			}
		}
	}

	&__price {
		@include addInfo;
		font-weight: 400;
		color: var(--brown-color);
	}

	&__blocks {
		display: flex;
		gap: 40px;

		@media (max-width: 578px) {
			flex-direction: column;

			img {
				width: 300px;
				height: 300px;
				object-fit: contain;
			}
		}
	}

	&__favorites {
		display: flex;
		align-items: center;
		gap: 16px;

		input {
			width: 25px;
			height: 25px;
		}

		p {
			@include titleUnder;
		}
	}

	&__basket {
		display: flex;
		gap: 24px;

		input {
			width: 100px;
			height: 50px;
			background-color: var(--silver-color);
			text-align: center;
			cursor: pointer;
		}

		a {
			max-width: 325px;
			width: 100%;
			height: 50px;
			border: 1px solid var(--line-color);
			text-transform: uppercase;
			background-color: transparent;
			transition: all 0.2s ease-in-out;
			color: var(--black-color);
			display: flex;
			align-items: center;
			justify-content: center;

			&:hover {
				background-color: var(--accent-pink-color);
				color: var(--white-color);
				border: none;
			}
		}
	}

	&__descript {
		@include descript;
	}

	&__line {
		width: 100%;
		background-color: #d9d9d9;
		height: 1px;
	}

	&__category {
		display: flex;
		align-items: center;
		margin-top: 24px;

		h1 {
			text-transform: uppercase;
			font-weight: 400;
			margin-right: 215px;
			@include descript2;
		}

		a {
			color: var(--brown-color);
			border-bottom: 1px solid var(--brown-color);
			padding-bottom: 1px;
			@include descript;
		}
	}

	&__response {
		margin-top: 120px;

		p {
			font-weight: 400;
			@include addInfo;
			color: var(--grey-color);
		}
	}

	// Swiper response styles
	&__response-blocks {
	}

	&__response-block {
		border: 1px solid var(--line-color);
		min-width: 545px;
		width: 100%;
		padding: 16px;
		display: flex;
		flex-direction: column;
		gap: 10px;
		margin-top: 40px;

		@media (max-width: 1200px) {
			min-width: 445px;
		}

		@media (max-width: 756px) {
			min-width: 400px;
		}

		@media (max-width: 500px) {
			min-width: 300px;
		}
	}

	&__response-top {
		display: flex;
		align-items: center;
		gap: 16px;

		img {
			width: 40px;
			height: 40px;
			object-fit: contain;
		}

		p {
			@include titleUnder2;
			text-transform: uppercase;
			font-weight: 400;
		}
	}

	&__response-stars {
		display: flex;
		gap: 6px;

		img {
			width: 18px;
			height: 18px;
			object-fit: contain;
		}
	}

	&__responce-descript p {
		@include descript;
		max-width: 500px;
		width: 100%;
		color: var(--black-color);
		font-weight: 300;
	}

	// Swiper styles
	.mySwiper-basket {
		width: 100%;
		padding-bottom: 30px; /* Оновлене значення для коректного відображення */
	}

	.swiper-pagination {
		display: flex;
		justify-content: center;
		gap: 16px;
	}

	.swiper-pagination-bullet {
		width: 30px;
		height: 3px;
		background-color: var(--disable-color);
		border-radius: 0;
		transition: background-color 0.3s ease;
	}

	.swiper-pagination-bullet-active {
		background-color: var(--brown-color);
	}

	.swiper-btns-2 {
		position: absolute;
		top: 50%;
	}

	.mySwiper-basket {
		width: 100%;
		padding-bottom: 30px;
	}
}

