.payment-modal {
	&__content {
		padding: 90px 30px 55px 30px;
		@media (max-width: 460px) {
			padding: 90px 20px 55px 20px;
		}
	}

	&__title {
		@include addInfo;
		font-weight: 500;
		text-align: center;
		margin-bottom: 30px;
		padding: 0 30px;
	}

	&__num {
		font-weight: 500;
		color: var(--brown-color);
		text-transform: uppercase;
		margin-bottom: 25px;
	}

	&__product {
		display: flex;
		align-items: center;
		min-height: 200px;
		@media (max-width: 460px) {
			flex-direction: column;
		}
		img {
			object-fit: cover;
			width: 158px;
			height: 200px;
			@media (max-width: 530px) {
				width: 130px;
			}
			@media (max-width: 460px) {
				width: 100%;
				height: 150px;
				max-width: 100%;
			}
		}

		@media (max-width: 530px) {
			// flex-direction: column;
		}
	}

	&__details {
		display: flex;
		flex-direction: column;
		gap: 8px;
		border: 1px solid var(--line-color);
		padding: 13px 16px 18px 36px;
		max-width: 390px;
		width: 100%;
		@media (max-width: 530px) {
			padding: 13px 16px 13px 16px;
		}
		div {
			display: flex;
			align-items: center;
			gap: 16px;
			@include descript;
			h1 {
				font-weight: 500;
				text-transform: uppercase;
			}
			span {
				text-transform: uppercase;
			}
		}
	}

	&__desc {
		font-size: 12px;
		color: var(--black-color);
		max-width: 255px;
		width: 100%;
	}

	&__cost {
		@include titleUnder2;
		font-weight: 500;
	}

	&__value {
		max-width: 75px;
		width: 100%;
		padding: 7px 15px;
		border: 1px solid var(--line-color);
	}

	&__summary {
		display: flex;
		flex-direction: column;
		margin-top: 15px;
		div {
			display: flex;
			align-items: center;
			justify-content: space-between;
			width: 100%;
			padding: 16px 0;
			border-bottom: 1px solid var(--line-color);
			p {
				@include titleUnder;
			}

			span {
				@include titleUnder2;
				color: var(--grey-color);
				font-weight: 400;
			}

			.green {
				color: #32ac37;
			}
		}
	}
}

// Modal 2
.payment-modal {
