	&__checkbox {
		display: flex;
		flex-direction: column;
		gap: 3px;

		input {
			width: 24px;
			height: 24px;
			border: 1px solid #a8a29e;
		}
		label {
			font-size: 12px;
			display: flex;
			align-items: center;
			gap: 8px;
		}
	}
}

