		width: 100%;
		margin: 0 auto;
	}

	&__close-btn {
		position: absolute;
		top: 13%;
		left: 20px;
	}
}

/* Стиль для анімації */
@keyframes slideOutLeft {
	0% {
		transform: translateX(0);
		opacity: 1;
	}
	100% {
		transform: translateX(-100%);
		opacity: 0;
	}
}

@keyframes slideInRight {
	0% {
		transform: translateX(100%);
		opacity: 0;
	}
	100% {
		transform: translateX(0);
		opacity: 1;
	}
}

.payment-step {
	width: 100%;
	opacity: 0;
	transform: translateX(100%);
	overflow: hidden;
	transition: opacity 0.3s, transform 0.3s;
}

.payment-step.active {
	opacity: 1;
	transform: translateX(0);
	z-index: 1;
}

.payment-step.hidden.animate-out {
	animation: slideOutLeft 0.5s forwards;
}

.payment-step.active.animate-in {
	animation: slideInRight 0.5s forwards;
}

// Анимация закрития Модального
@keyframes modalFadeOut {
	0% {
		opacity: 1;
		transform: translateX(0);
	}
	100% {
		opacity: 0;
		transform: translateX(100%);
	}
}

.payment-modal {
	opacity: 1;
	transform: translateX(0);
	transition: opacity 0.3s, transform 0.3s;
}

.payment-modal.animate-close {
	animation: modalFadeOut 0.5s forwards;
}

// Откритія меню
.payment-modal.animate-open {
	display: block;
	animation: modalFadeIn 0.5s forwards;
}

.no-scrollModal {
	overflow: hidden;
	position: relative;
	&::before {
		position: absolute;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.5);
		content: "";
		left: 0;
		top: 0;
		z-index: 999;
	}
}

.animate-open {
	animation: fadeIn 0.5s ease-in-out;
}

.animate-close {
	animation: fadeOut 0.5s ease-in-out;
}

@keyframes fadeIn {
	0% {
		opacity: 0;
		transform: scale(0.9);
	}
	100% {
		opacity: 1;
		transform: scale(1);
	}
}

@keyframes fadeOut {
	0% {
		opacity: 1;
		transform: scale(1);
	}
	100% {
		opacity: 0;
		transform: scale(0.9);
	}
}

.payment-modal__checkbox label {
	font-size: 15px;
	display: flex;
	align-items: center;
	gap: 10px;
	margin-bottom: 10px;
	color: var(--black-color);

	@media (max-width: 668px) {
		font-size: 10px;
	}
}

input::placeholder {
	color: var(--dark-grey-color);
	font-size: 16px;

	@media (max-width: 668px) {
		font-size: 13px;
	}
}

input[type="checkbox"] {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	min-width: 20px;
	background-color: transparent;
	border: 2px solid #a8a29e;
	display: inline-block;
	position: relative;
	cursor: pointer;
}

input[type="checkbox"]:checked {
	background-color: transparent;
}

input[type="checkbox"]:checked::after {
	content: "";
	position: absolute;
	top: 35%;
	left: 50%;
	transform: translate(-50%, -50%) rotate(40deg);
	width: 8px;
	height: 17px;
	border: solid #d97706;
	border-width: 0 2px 2px 0;
}

/* Стиль для вимкненого чекбокса */
input[type="checkbox"]:disabled {
	border-color: lightgray;
	cursor: not-allowed;
}

/* Галочка для вимкненого чекбокса */
input[type="checkbox"]:disabled:checked::after {
	border-color: lightgray;
}
