html,
body {
	height: 100%;
}

html {
	scroll-behavior: smooth;
}

body {
	&.no-scroll1 {
		overflow: hidden;
	}
	color: var(--black-color);
}

*,
*::after,
*::before {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

body {
	font-family: "Jost", sans-serif;
	font-weight: 300;
	color: var(--black-color);
	background-color: var(--white-color);
}

[class*="__container"] {
	max-width: 1145px;
	margin: 0 auto;
	padding: 0 15px;
}

// Прижатие футера к низу
.wrapper {
	min-height: 100%;
	display: flex;
	flex-direction: column;
}

.wrapper-container {
	flex: 1 1 auto;
}

// активное меню

.menu a {
	&.active-page {
		color: var(--gold-color);
	}
}

//

.read-more {
	color: var(--brown-color);
	font-size: 16px;
	width: auto;
	position: relative;
}

.author-btn {
	font-size: 16px;
	color: var(--brown-color);
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
	opacity: 1;
}

