.articles {
	padding: 130px 0 55px 0;
	overflow: hidden;
	position: relative;

	&__title {
		text-align: center;
		margin-top: 35px;
		@include title;
		margin-bottom: 56px;
		@media (max-width: 992px) {
			margin-bottom: 30px;
		}
	}

	&__slider-blocks {
		display: grid;
	}

	.swiper-container {
		overflow: hidden;
		width: 100%;
		// height: auto;
		margin-bottom: 56px;
		justify-items: center;
	}

	.swiper-pagination {
		position: absolute;
		margin-bottom: 30px;
		bottom: 10%;
	}

	.swiper-slide {
		display: flex;
		align-items: center;
		justify-content: center;
		a {
			color: var(--black-color);
		}
	}

	.swiper-pagination {
		display: flex;
		justify-content: center;
		gap: 16px;
	}

	.swiper-pagination-bullet {
		width: 30px;
		height: 3px;
		background-color: var(--disable-color);
		// margin: 0 15px;
		border-radius: 0px;
	}

	.swiper-pagination-bullet-active {
		background-color: var(--brown-color);
		width: 30px;
		height: 3px;
		border-radius: 0px;
		// border-radius: 0px;
	}

	&__slider-block {
		display: flex;
		flex-direction: column;
		max-width: 350px;
		width: 100%;
		gap: 10px;
		justify-items: center;
		user-select: none;

		&__slider-img {
			width: 100%;
			object-fit: cover;
		}
	}

	&__slider-content {
		display: flex;
		flex-direction: column;
		h1 {
			@include titleUnder;
			margin-bottom: 4px;
		}
	}

	&__slider-info {
		display: flex;
		align-items: center;
		gap: 32px;
		@media (max-width: 768px) {
			gap: 20px;
		}
		div {
			display: flex;
			align-items: center;
			gap: 8px;
		}

		img {
			width: 20px;
			height: 20px;
			object-fit: contain;
			@media (max-width: 992px) {
				width: 15px;
				height: 15px;
			}
		}

		p {
			color: var(--grey-color);
			@include descript2;
		}
	}
}

.articles-single {
	&__header {
		height: 700px;
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
		background-attachment: fixed;
		background-image: url("/images/articles-page/articles-picture.png");
	}

	&__blocks {
		padding: 80px 0 55px 0;
	}

	&__title {
		margin-top: 56px;
		@include title;
		margin-bottom: 8px;
	}

	&__info {
		display: flex;
		align-items: center;
		gap: 32px;
		margin-bottom: 40px;
		li {
			display: flex;
			align-items: center;
			gap: 8px;
		}

		@media (max-width: 567px) {
			flex-direction: column;
			gap: 8px;
			text-align: left;
			align-items: start;
		}
	}

	&__content {
		display: flex;
		flex-direction: column;
		gap: 16px;
		max-width: 837px;
		width: 100%;
		@include descript;
		line-height: 140%;
		margin-bottom: 24px;
	}

	&__link {
		color: var(--brown-color);
		@include descript;
		border-bottom: 1px solid var(--brown-color);
	}
}
