.breadcrumb {
	display: flex;
	list-style: none;
	margin: -25px 0;
}

.breadcrumb-item {
	margin-right: 8px;
	@include titleUnder2;
}

.breadcrumb-item a {
	color: var(--grey-color);
}

.breadcrumb-item a:hover {
	text-decoration: underline;
}

.breadcrumb-item + .breadcrumb-item::before {
	content: "/";
	margin-right: 8px;
	color: #6c757d;
}

.breadcrumb-item.active {
	pointer-events: none;
	a {
		color: var(--black-color);
	}
}
